import * as React from 'react';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { TableRow, TableCell, Grid } from '@material-ui/core';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { faSort } from '@fortawesome/free-solid-svg-icons';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';

interface IStates {
    loaId: string;
    approvalCode: string;
    diagnosis: string;
    procedure: string;
    provider: string;
    amount: string;
    sessions: string;
    dateAvailed: string;
    type: string;
    status: string;
}


export class CheckUtilizationSection extends React.Component<{}, IStates> {

    dummyData = [
        {
            loa: { id: '9999-99999', swipe: true },
            approvalCode: '99999999',
            diagnosis: { code: '999.99', name: 'ICD10 Name', desc: 'Description.' },
            procedure: { code: '999.99', name: 'CPT Name', desc: 'Description.' },
            provider: { name: 'General Santos Doctor’s Hospitals.' },
            amount: 0,
            sessions: 1,
            dateAvailed: new Date(),
            type: 'IBNR',
            status: 'Approved'
        }
    ]

    constructor(props: any) {
        super(props);

        this.state = {
            loaId: '',
            approvalCode: '',
            diagnosis: '',
            procedure: '',
            provider: '',
            amount: '',
            sessions: '',
            dateAvailed: '',
            type: '',
            status: ''
        };
    }
    

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Check Utilization</div>
                <Grid container className='sub-section' spacing={2}>
                    <Grid item xs={3}>
                        <div className='light-text'>ANNUAL BENEFIT LIMIT</div>
                        <div className='header-1'>{'PHP 0.00'}</div>
                    </Grid>
                    <Grid item xs={9}>
                        <Line 
                            items={[
                                { value: 70, color: '#3FBB7F', label1: 'UTILIZED', label2: 'PHP 38,021.35' },
                                { value: 5, color: '#1D236F', label1: 'IBNR', label2: 'PHP 11,978.65' },
                                { value: 25, color: '#FCB53F', label1: 'AVAILABLE', label2: 'PHP 50,001' }
                            ]}
                            maxValue={100} />
                    </Grid>
                </Grid>
                <div className='sub-section' style={{overflowX: 'auto'}}>
                    <CustomTable
                        className='custom-table-scrollable'
                        rows={this.dummyData}
                        renderHeader={() => this.renderTableHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderTableRow(rowData, index)} 
                        rowsPerPageOptions={[10, 25, 50]} />
                </div>
                <div className='section' />
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            {this.renderHeaderColumn('loaId', 'LOA ID', '100px')}
            {this.renderHeaderColumn('approvalCode', 'Approval Code', '120px')}
            {this.renderHeaderColumn('diagnosis', 'Diagnosis', '200px')}
            {this.renderHeaderColumn('procedure', 'Procedure', '200px')}
            {this.renderHeaderColumn('provider', 'Provider', '150px')}
            {this.renderHeaderColumn('amount', 'Amount', '100px')}
            {this.renderHeaderColumn('sessions', 'Sessions', '75px')}
            {this.renderHeaderColumn('dateAvailed', 'Date Availed', '150px')}
            {this.renderHeaderColumn('type', 'Type', '75px')}
            {this.renderHeaderColumn('status', 'Status', '75px')}
            </>
        );
    }

    renderTableRow(utilization: any, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{utilization.loa.id}</TableCell>
                <TableCell>{utilization.approvalCode}</TableCell>
                <TableCell>{utilization.diagnosis.name}</TableCell>
                <TableCell>{utilization.procedure.name}</TableCell>
                <TableCell>{utilization.provider.name}</TableCell>
                <TableCell>{utilization.amount}</TableCell>
                <TableCell>{utilization.sessions}</TableCell>
                <TableCell>{GlobalFunction.toCommonDateString(utilization.dateAvailed)}</TableCell>
                <TableCell>{utilization.type}</TableCell>
                <TableCell>{utilization.status}</TableCell>
            </TableRow>
        );
    }


    renderHeaderColumn(attrName: string, headerText: string, width: string): JSX.Element {
        return (
            <TableCell style={{minWidth: width, maxWidth: width}}>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            // this.sortTickets('urgency', true);
                            // this.setState({ticketListUpdated: true});
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        this.setState(prevState => ({...prevState, [attrName]: value}));
                    }} />
            </TableCell>
        );
    }
}


interface LineProps {
    items: {
        value: number;
        color: string;
        label1: string;
        label2: string;
    }[]
    maxValue: number;
}

export class Line extends React.Component<LineProps> {

    render(): JSX.Element {
        return (
            <div style={{width: '50%', height: 0}}>
                {this.props.items.map(item => {
                    return (
                        <div 
                            style={{
                                width: (item.value / this.props.maxValue * 100) + '%', 
                                height: '6px', 
                                display: 'inline-block',
                                backgroundColor: item.color
                            }} />
                    );
                })}
                <div style={{padding: '0.5rem 0'}}>
                {this.props.items.map(item => {
                    return (
                        <div 
                            style={{
                                width: (1 / this.props.items.length * 100) + '%', 
                                height: '6px', 
                                display: 'inline-block'
                            }}>
                            <div>
                                <div 
                                    style={{
                                        width: '10px', 
                                        height: '10px', 
                                        backgroundColor: item.color,
                                        display: 'inline-block',
                                        marginRight: '1rem'
                                    }}/>
                                <div 
                                    className='light-text' 
                                    style={{
                                        display: 'inline-block'
                                    }}>
                                    {item.label1}
                                </div>
                                <div 
                                    className='header-2' 
                                    style={{
                                        color: item.color,
                                    }}>
                                    {item.label2}
                                </div>
                            </div>
                         </div>
                    );
                })}
                </div>
            </div>
        );
    }
}