
import * as React from 'react';
import { Grid, TableCell, TableRow } from '@material-ui/core';
import { InquiryData } from 'Pages/CustomerCarePage/Models';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';

interface IProps {
    inquiries: InquiryData[];
}

export class ViewInquirySection extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Details</div>
                <div className='text'>
                    Add an inquiry type and then add the question and its details after
                </div>
                <div className='sub-section'>
                    {true ? (
                        <CustomTable 
                            className='custom-table'
                            rows={this.props.inquiries}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderTableRow(rowData, index)} />
                    ) : (null)}
                </div>
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (<TableCell>Inquiries</TableCell>);
    }

    renderTableRow(inquiry: InquiryData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        const detail = inquiry.detail.length > 0 ? inquiry.detail : '---';
        const specificDetail = inquiry.specificDetail.length > 0 ? inquiry.specificDetail.join(', ') : '---';
        const answer = inquiry.answer.length > 0 ? inquiry.answer : '---';
        const remarks = inquiry.remarks.length > 0 ? inquiry.remarks : '---';
        const relatedTickets = inquiry.relatedTickets.length > 0 ? inquiry.relatedTickets.join(', ') : '---';

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell colSpan={2}>
                    <Grid container spacing={1}>
                        <Grid item xs={12} className='header-2'>
                            {inquiry.inquiry.label}
                        </Grid>
                    </Grid>
                    <div style={{position: 'relative'}}>
                        <div style={{paddingTop: '0.5rem'}} />
                        <div className='line-border' />
                        <div style={{paddingTop: '0.5rem'}} />
                        <Grid container spacing={2}>
                            <Grid item xs={6}>
                                <div className='light-text'>Details of inquiry (Question)</div>
                                <div className='small-text'>{detail}</div>
                            </Grid>
                            <Grid item xs={6}>
                                <div className='light-text'>Specific Details</div>
                                <div className='small-text'>{specificDetail}</div>
                            </Grid>
                            <Grid item xs={6}>
                                <div>
                                    <Grid container spacing={1}>
                                        <Grid item xs={12}>
                                            <div className='light-text'>Answer</div>
                                            <div className='small-text'>{answer}</div>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <div className='light-text'>Remarks (Optional)</div>
                                            <div className='small-text'>{remarks}</div>
                                        </Grid>
                                        <Grid item />
                                        <Grid item xs={12}>
                                            <div className='light-text'>Related Tickets</div>
                                            <div className='small-text'>{relatedTickets}</div>
                                        </Grid>
                                    </Grid>
                                </div>
                            </Grid>
                            <Grid item xs={6}>
                                <div className='light-text'>Supporting Documents</div>
                                {inquiry.supportDocs.map(doc => {
                                    return (
                                        <div className='small-text'>
                                            <TextButton 
                                                label={doc.name} 
                                                onClick={() => {}}/>
                                        </div>
                                        
                                    );
                                })}
                            </Grid>
                        </Grid>
                    </div>
                </TableCell>
            </TableRow>
        );
    }
}