import * as React from 'react';
import { TableRow, TableCell } from '@material-ui/core';
import { ClientData, MemberData } from 'Pages/CustomerCarePage/Models';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { API } from 'Pages/CustomerCarePage/API';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { faSort } from '@fortawesome/free-solid-svg-icons';

interface IProps {
    client: ClientData;
}

interface IStates {
    members?: MemberData[];
}

export class MembersSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {

        };
    }

    componentDidMount() {
        const filter = {
            where: {
                client_id: this.props.client.id//'5d9af14586cd6f0349131e88'
            }
        };
        API.get(`members?filter=${JSON.stringify(filter)}`,
            (response) => {
                console.log(response);
                this.setState({members: Payloads.getMembers(response)});
            },
            (error) => {
                console.log(error);
            });
    }

    render() : JSX.Element {

        return (
            <div className='sub-section' style={{overflowX: 'auto'}}>
                {this.state.members ? (
                    <CustomTable
                        rows={this.state.members}
                        renderHeader={() => this.renderTableHeader()}
                        rowsPerPageOptions={[10, 25, 50]}
                        renderRow={(rowData: any, index: number) => 
                            this.renderTableRow(rowData, index)} />
                ) : (
                    <Loading height='10rem' />
                )}
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            {this.renderHeaderColumn('memberId', 'Member ID', '200px')}
            {this.renderHeaderColumn('filterBenefits', 'Name')}
            {this.renderHeaderColumn('filterPrincipals', 'Corporate/s')}
            {this.renderHeaderColumn('filterDependents', 'Effectivity Date')}
            {this.renderHeaderColumn('filterDateCreated', 'Termination Date')}
            {this.renderHeaderColumn('filterVersion', 'Status')}
            </>
        );
    }

    renderTableRow(member: MemberData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        const effectivityDate = member.effectivityDate ? GlobalFunction.toShortDateString(member.effectivityDate) : '---';
        const terminationDate = member.terminationDate ? GlobalFunction.toShortDateString(member.terminationDate) : '---';
        const name = GlobalFunction.toNameFormat(
            member.firstName,
            member.middleName,
            member.lastName
        );

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{member.memberId}</TableCell>
                <TableCell>{name}</TableCell>
                <TableCell>{member.corporateName || '---'}</TableCell>
                <TableCell>{effectivityDate || '---'}</TableCell>
                <TableCell>{terminationDate}</TableCell>
                <TableCell>{member.status || '---'}</TableCell>
            </TableRow>
        );
    }

    renderHeaderColumn(attrName: string, headerText: string, width?: string): JSX.Element {
        return (
            <TableCell style={{minWidth: width ? width : '150px'}}>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            // this.sortFacilities(attrName, true);
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        // @ts-ignore
                        this.setState({[attrName]: value});
                    }} />
            </TableCell>
        );
    }
}