import * as React from 'react';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { Grid } from '@material-ui/core';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { DetailedNumberField } from 'Pages/CustomerCarePage/Components/DetailedNumberField';
import { DatePicker } from 'Pages/CustomerCarePage/Components/DatePicker';
import { DropdownChoice } from 'Pages/CustomerCarePage/Models';

interface IProps {
    dateAdmitted: Date;
    coverage: DropdownChoice;
    ticketNo: string;
    paidBy: string;
    philhealthAmount: number;
    finalBill: number;
    dischargeDate?: Date;
    onUpdateTicketStateChange: (attrName: string, value: any) => void;
}

export class DischargeDetailsSection extends React.Component<IProps> {

    coverageList: DropdownChoice[];

    constructor(props: any) {
        super(props);

        this.coverageList = [
            { value: '1', label: 'Filed' },
            { value: '2', label: 'Corporate Guarantee' },
            { value: '3', label: 'Not Compensable' },
            { value: '4', label: 'Less than 24 hours' },
            { value: '5', label: 'Paid' },
            { value: '6', label: 'PH Rider' },
            { value: '7', label: 'Non-PNDF' }
        ]
    }


    render(): JSX.Element {

        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Set PhilHealth</div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <Dropdown 
                                placeholder='Coverage'
                                choices={this.coverageList}
                                value={this.props.coverage}
                                onChange={(value: DropdownChoice) => {
                                    this.props.onUpdateTicketStateChange('coverage', value);
                                }} />
                        </Grid>
                        {this.props.coverage.label === 'Corporate Guarantee' ? (
                            <Grid item xs={3}>
                                <InputField 
                                    placeholder='Ticket Number'
                                    value={this.props.ticketNo}
                                    onChange={(value: string) => {
                                        this.props.onUpdateTicketStateChange('ticketNo', value);
                                    }} />
                            </Grid>
                        ) : null}
                        {this.props.coverage.label === 'Paid' ? (
                        <Grid item xs={3}>
                            <InputField 
                                placeholder='Paid by'
                                value={this.props.paidBy}
                                onChange={(value: string) => {
                                    this.props.onUpdateTicketStateChange('paidBy', value);
                                }} />
                        </Grid>
                        ) : null}
                        {this.props.coverage.label === 'Filed' ||
                            this.props.coverage.label === 'Corporate Guarantee' ||
                            this.props.coverage.label === 'Paid' ? (
                            <Grid item xs={3}>
                                <DetailedNumberField 
                                    placeholder='Amount'
                                    detail='PHP'
                                    isFloat={true}
                                    value={this.props.philhealthAmount}
                                    onChange={(value: number) => {
                                        this.props.onUpdateTicketStateChange('philhealthAmount', value);
                                    }} />
                            </Grid>
                        ) : null}

                        
                        
                    </Grid>
                </div>
                <div className='header-1'>Add Discharge Details</div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={3}>
                            <DetailedNumberField 
                                placeholder='Final Hospital Bill Amount'
                                detail='PHP'
                                isFloat={true}
                                value={this.props.finalBill}
                                onChange={(value: number) => {
                                    this.props.onUpdateTicketStateChange('finalBill', value);
                                }} />
                        </Grid>
                        <Grid item xs={3}>
                            <DatePicker 
                                placeholder='Discharge Date'
                                value={this.props.dischargeDate || null}
                                minDate={this.props.dateAdmitted}
                                onChange={(value: Date) => {
                                    this.props.onUpdateTicketStateChange('dischargeDate', value);
                                }} />
                        </Grid>
                    </Grid>
                </div>
                <div className='section' />
            </div>
        );
    }
}