import * as React from 'react';
import { CRUDTable } from 'Pages/CustomerCarePage/UpdateTicketPage/Components/CRUDTable';
import { Grid } from '@material-ui/core';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomDropzone } from 'Pages/CustomerCarePage/Components/CustomDropzone';
import { FileData } from '../UpdateTicketProps';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';

interface IProps {
    files: FileData[];
    state: FileData;
    refreshFilesStateChange: () => void;
}

export class UploadedDocumentsSection extends React.Component<IProps> {

    render(): JSX.Element {console.log(this.props.files);
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Upload Documents</div>
                <div className='sub-section'>
                    <Grid container>
                        <Grid item xs={6}>
                            <CustomDropzone
                                accept='image/*'
                                onDrop={(files: File[]) => {
                                    this.props.state.file = files[0];
                                    this.props.refreshFilesStateChange();
                                }} />
                            <div>{this.props.state.file ? this.props.state.file.name : null}</div>
                            <div className='sub-section' />
                            <InputField 
                                placeholder='Document Name' 
                                value={this.props.state.docName}
                                onChange={(value: string) => {
                                    this.props.state.docName = value;
                                    this.props.refreshFilesStateChange();
                                }} />
                        </Grid>
                        <Grid item xs={6} />
                    </Grid>
                    <div style={{paddingTop: '5px'}} />
                    <TextButton 
                        label='Add document' 
                        icon={faPlus}
                        onClick={() => {
                            var state = this.props.state;
                            if (state.file !== null) {
                                const tempData = {
                                    docName: state.docName,
                                    file: state.file
                                }
                                this.props.files.unshift(tempData);
                                this.props.state.docName = '';
                                this.props.state.file = null;
                                this.props.refreshFilesStateChange();
                            }
                        }}/>
                </div>
                
                <div className='sub-section'>
                    <CRUDTable 
                        className='custom-table'
                        placeholder='UPLOADED DOCUMENTS'
                        
                        cols={['Document Name', 'Filename']} 
                        rows={this.props.files}
                        onSaveEditRow={(index: number, data: any) => {
                            // this.props.editFile(index, data);
                            
                            this.props.files[index] = data;
                            this.props.refreshFilesStateChange();
                        }}
                        onDeleteRow={(index: number) => {
                            // this.props.deleteFile(index);
                            this.props.files.splice(index, 1);
                            this.props.refreshFilesStateChange();
                        }}
                        onFormat={(_rowIndex: number, colIndex: number, value: any) => {
                            return (colIndex === 1 ? (value.name) : (value));
                        }}
                        editField={(colIndex: number, currentVal: any, onChange: any) => {
                            if (colIndex === 0) {
                                return (
                                    <InputField
                                        value={currentVal}
                                        onChange={(value: string) => onChange(value)}/>
                                );
                            }
                            return currentVal.name;
                        }} />
                </div>

                <div className='section' />
            </div>
        );
    }
}