import * as React from 'react';
import Dropzone from 'react-dropzone';
import './component-style.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileUpload } from '@fortawesome/free-solid-svg-icons';

interface IProps {
    placeholder?: string;
    accept?: string;
    onDrop: (files: File[]) => void;
}

export class CustomDropzone extends React.Component<IProps> {

    shouldComponentUpdate(props: IProps, _state: any) {
        return (this.props.placeholder !== props.placeholder);
    }

    render(): JSX.Element {
        return (
            <div className='custom-uploader'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : (null)}
                <div className='upload-box'>
                    <Dropzone
                        multiple={false}
                        onDrop={acceptedFiles => this.props.onDrop(acceptedFiles)}
                        accept={this.props.accept}
                        // accept="application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                        >
                        {({getRootProps, getInputProps}) => (
                            <section>
                                <div className='upload-file' {...getRootProps()}>
                                    <input {...getInputProps()} />
                                    <FontAwesomeIcon className='fa-1.5x' icon={faFileUpload} />
                                    <div>Drag or drop file to upload a <br />supporting document</div>
                                </div>
                            </section>
                        )}
                    </Dropzone>
                </div>
                

            </div>
        );
    }
}