import * as React from 'react';
import { ClientData } from 'Pages/CustomerCarePage/Models';
import { CustomCheckbox } from 'Pages/CustomerCarePage/Components/CustomCheckbox';
import { Grid } from '@material-ui/core';

interface IProps {
    client: ClientData;
}

export class EnrollmentSection extends React.Component<IProps> {

    render() : JSX.Element {
        const enrollment = this.props.client.servicingInstructions ? 
                           this.props.client.servicingInstructions.enrollment : 
                           undefined;

        const memberClientEncoding = enrollment && 
                                     enrollment.dependentEntrollment ?
                                     enrollment.dependentEntrollment.find(i => i.enrollment === 'member client encoding') :
                                     undefined;

        return (
            <div className='sub-section'>
                <div className='sub-section'>
                    <div className='text'>Employee Enrollment</div>
                    <CustomCheckbox
                        label='HR Client uploading'
                        disabled
                        value={
                            enrollment && 
                            enrollment.employeeEntrollment ? 
                            enrollment.employeeEntrollment.includes('hr client uploading') : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='HR Client encoding'
                        disabled
                        value={
                            enrollment && 
                            enrollment.employeeEntrollment ? 
                            enrollment.employeeEntrollment.includes('hr client encoding') : false}
                        onChange={(_value: boolean) => {}} />
                </div>
                <div className='sub-section'>
                    <div className='text'>Dependent Enrollment</div>
                    <CustomCheckbox
                        label='HR Client uploading'
                        disabled
                        value={
                            enrollment && 
                            enrollment.dependentEntrollment ? 
                            enrollment.dependentEntrollment.find(i => i.enrollment === 'hr client uploading') ? true : false : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='HR Client encoding'
                        disabled
                        value={
                            enrollment && 
                            enrollment.dependentEntrollment ? 
                            enrollment.dependentEntrollment.find(i => i.enrollment === 'hr client encoding') ? true : false : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Member Client encoding'
                        disabled
                        value={
                            enrollment && 
                            enrollment.dependentEntrollment ? 
                            enrollment.dependentEntrollment.find(i => i.enrollment === 'member client encoding') ? true : false : false}
                        onChange={(_value: boolean) => {}} />
                    <Grid container>
                        <Grid item xs={1} />
                        <Grid item xs={11}>
                            <CustomCheckbox
                                label='Upload supporting documents required'
                                disabled
                                value={
                                    memberClientEncoding &&
                                    memberClientEncoding.options ? 
                                    memberClientEncoding.options.includes('upload supporting') : false}
                                onChange={(_value: boolean) => {}} />
                            <CustomCheckbox
                                label='Submission of physical supporting documents required'
                                disabled
                                value={
                                    memberClientEncoding &&
                                    memberClientEncoding.options ? 
                                    memberClientEncoding.options.includes('submission of physical') : false}
                                onChange={(_value: boolean) => {}} />
                            <CustomCheckbox
                                label='Approval of HR through HR Client Portal required'
                                disabled
                                value={
                                    memberClientEncoding &&
                                    memberClientEncoding.options ? 
                                    memberClientEncoding.options.includes('approval of hr') : false}
                                onChange={(_value: boolean) => {}} />
                        </Grid>
                    </Grid>
                </div>
                <div className='sub-section'>
                    <div className='text'>Enrollment done through</div>
                    <CustomCheckbox
                        label='Email'
                        disabled
                        value={
                            enrollment && 
                            enrollment.enrollmentDoneThrough ? 
                            enrollment.enrollmentDoneThrough.includes('email') : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Physical documents'
                        disabled
                        value={
                            enrollment && 
                            enrollment.enrollmentDoneThrough ? 
                            enrollment.enrollmentDoneThrough.includes('physical documents') : false}
                        onChange={(_value: boolean) => {}} />
                </div>
            </div>
        );
    }
}