import * as React from 'react';
import './component-style.scss';
import { DropdownChoice } from '../Models';
import Select from 'react-select';

interface IProps {
    placeholder: string;
    choices: DropdownChoice[];
    type: DropdownChoice;
    value: string;
    onChangeType: (type: DropdownChoice) => void;
    onChangeValue: (value: string) => void;
}

export class TypeValueField extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <div className='detailed-input-field'>
                <div className='text'>{this.props.placeholder}</div>
                <div className='input-field'>
                        <Select
                            className='actual-dropdown'
                            classNamePrefix='actual-dropdown'
                            value={this.props.type}
                            options={this.props.choices} 
                            onChange={(choice: DropdownChoice) => {
                                this.props.onChangeType(choice);
                            }} />
                    <input 
                        className='text-field right' 
                        value={this.props.value}
                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                            this.props.onChangeValue(event.target.value);
                        }} />
                </div>
            </div>
        );
    }
}