import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { Processmaker } from 'Pages/CustomerCarePage/Processmaker';
import { DropdownChoice, TicketData, CaseData } from 'Pages/CustomerCarePage/Models';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { API } from 'Pages/CustomerCarePage/API';

interface IProps {
    caseId: string;
    ticket: TicketData;
    currentCase: CaseData;
    isAssignModalOpen: boolean;
    onClose: () => void;
    onSuccess: () => void;
}

interface IStates {
    assignee: DropdownChoice;
    notes: string;
}

export class ReassignTicketModal extends React.Component<IProps, IStates> {

    usersList: DropdownChoice[] = [
        { value: '7126065025dadc5aeb748d0045044154', label: 'BDO USERS', params: [true] },
        { value: '2998254655daee1a5d297c2012850821', label: 'BDO User 1', params: [false] },
        { value: '2354299535daedca198c724071777008', label: 'CSS Group', params: [true] },
        { value: '1450007145db54b7ebfa6a8025711962', label: 'CSS User 1', params: [false] },
        { value: '4083355895e36a11e5f7264011499223', label: 'PRO User 1', params: [false] },
        { value: '2620353445dbd5157b3b9d8070237919', label: 'CSD TL 1', params: [false] },
        { value: '7702946405dbd516f1a2715091971857', label: 'CSD TL 2', params: [false] }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            assignee: { value: '', label: '' },
            notes: ''
        };
    }

    onReassign = () => {
        const assignee = this.state.assignee;
        const ticket = this.props.ticket;

        const body = {
            button_pressed: 'ASSIGN',
            next_user: assignee.value,
            assign_to_group: assignee.params && assignee.params.length > 0 ? assignee.params[0] : false,
            assigned_by: `${localStorage['first_name']} ${localStorage['last_name']}`,
            assign_remarks: this.state.notes
        };

        const patchBody = {
            assign_to: assignee.label
        };

        API.patch(`customer-care/tickets/${ticket.id}`, patchBody, () => {
            Processmaker.shortcutCall('PUT', `cases/${this.props.caseId}/variable`, body, () => {
                Processmaker.shortcutCall('PUT', `cases/${this.props.caseId}/route-case`, {}, () => {
                    if (this.props.currentCase.task === 'Inquiry - Comment') {
                        this.props.onSuccess();
                    } else {
                        Processmaker.shortcutCall('PUT', `cases/${this.props.caseId}/route-case`, {}, () => {
                            this.props.onSuccess();
                        });
                    }
                });
            });
        });
        
    }

    render(): JSX.Element {
        return (
            <CustomModal
                className='modal'
                isOpen={this.props.isAssignModalOpen}
                handleClose={() => this.props.onClose()}
                header='Re-assign Ticket'
                content={
                    <>
                    <div style={{padding: '0.5rem 0'}}>
                        {'Re-assigning this ticket to another user will allow him/here to manage this ticket instead.'}
                    </div>
                    <div style={{padding: '0.5rem 0'}}>
                        <Dropdown 
                            placeholder='Enter New Asignee'
                            choices={this.usersList}
                            value={this.state.assignee}
                            onChange={(value: DropdownChoice) => {
                                this.setState({assignee: value});
                            }} />
                    </div>
                    <div style={{padding: '0.5rem 0'}}>
                        <InputArea 
                            placeholder='Notes'
                            defaultRows={4}
                            value={this.state.notes}
                            onChange={(value: string) => {
                                this.setState({notes: value});
                            }} />
                    </div>
                    </>
                }
                footer={
                    <>
                    <CustomButton
                            text='Cancel'
                            variant='outlined'
                            onClick={() => {
                                this.props.onClose()
                            }} />
                        <span style={{padding: '1rem'}} />
                    <CustomButton 
                        text='Re-assign'
                        variant='contained'
                        onClick={() => {
                            // this.props.onClose();
                            this.onReassign();
                        }} />
                    </>
                } />
        );
    }
}