import * as React from 'react';
import { API } from 'Pages/CustomerCarePage/API';

interface IProps {
    memberId?: string;
    clientId?: string;
}

interface IStates {
    contractListUpdated: boolean;
}

export class CheckContractSection extends React.Component<IProps, IStates> {

    contractUrls: string[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            contractListUpdated: false
        }

        this.contractUrls = [];
    }

    componentDidMount() {
        API.get(`customer-care/members/${this.props.memberId || this.props.clientId}/benefits`,
            (response) => {
                console.log(response);
                this.contractUrls = response.map(benefit => {
                    return `/customer-care/benefits/io/pdf?id=${benefit._id}&type=contract`
                });

                this.setState({contractListUpdated: true});
            },
            (error) => {
                console.log(error);
            });
    }
    
    render(): JSX.Element {
        const pdfUrl = `${process.execPath}/../../../sample.pdf`;
        // const viewerUrl = `https://mozilla.github.io/pdf.js/web/viewer.html?file=${pdfUrl}`;
        const viewerUrl = `${process.execPath}/../pdfjs-2.2.228-dist/web/viewer.html?file=${pdfUrl}`;

        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Check Contract</div>
                <div className='sub-section'>
                    {/*this.contractUrls.map((url, index) => {
                        return (
                            index === 0 ?
                            <iframe 
                                style={{width: '100%', height: '1280px'}}
                                src={API.getUrl() + url}  />

                            : null
                        );
                    })*/}
                            <iframe 
                                style={{width: '100%', height: '1280px'}}
                                src={viewerUrl}  />
                </div>
                
            </div>
        );
    }
}