import * as React from 'react';
import './component-style.scss';
import { Tabs, Tab } from '@material-ui/core';

interface IProps {
    tabList: { value: number, label: string }[];
    selectedTab: number;
    orientation?: 'horizontal' | 'vertical';
    tabStyle?: (index: number) => React.CSSProperties;
    tabLabel?: (index: number) => React.ReactNode;
    setSelectedTab: (index: number) => void;
}

export class TextTab extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <Tabs
                className='text-tabs' 
                value={this.props.selectedTab}
                orientation={this.props.orientation}
                variant='scrollable'
                scrollButtons='auto'
                indicatorColor='primary'
                onChange={(_event: React.ChangeEvent<{}>, value: number) => {
                    this.props.setSelectedTab(value);
                }}>
                {this.props.tabList.map((tab, index) => {
                    return (
                        <Tab 
                            style={this.props.tabStyle ? this.props.tabStyle(index) : undefined}
                            label={this.props.tabLabel ? this.props.tabLabel(index) : tab.label} 
                            value={tab.value} />
                    )
                })}
            </Tabs>
        );
    }
}