import * as React from 'react';
import './component-style.scss';
import { Tabs, Tab } from '@material-ui/core';

interface IProps {
    tabList: { value: number, label: string }[];
    selectedTab: number;
    setSelectedTab: (index: number) => void;
}

export class CustomTabs extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <Tabs
                className='tabs' 
                value={this.props.selectedTab}
                variant='scrollable'
                scrollButtons='auto'
                indicatorColor='primary'
                onChange={(_event: React.ChangeEvent<{}>, value: number) => {
                    this.props.setSelectedTab(value );
                }}>
                {this.props.tabList.map(tab => {
                    return <Tab value={tab.value} label={tab.label} />
                })}
            </Tabs>
        );
    }
}