import * as React from 'react';
// import { Select, MenuItem } from '@material-ui/core';
import Select from 'react-select';
import './component-style.scss';
import { DropdownChoice } from '../Models';
import { Option } from 'react-select/src/filters';

interface IProps {
    placeholder?: string;
    disabled?: boolean;
    detail?: string;
    value: DropdownChoice;
    choices: DropdownChoice[];
    error?: boolean;
    onChange: (value: DropdownChoice) => void;
}

interface IStates {
    value: string;
}

export class DetailedDropdown extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            value: ''
        }
    }

    shouldComponentUpdate(props: IProps, _state: IStates) {
        return (
            this.props.choices.length !== props.choices.length ||
            this.props.value.value !== props.value.value
        );
    }

    render(): JSX.Element {
        return (
            <div className='detailed-dropdown'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : null}
                
                <div className='autocomplete'>
                    <div className={
                        this.props.disabled ? 'detail-disabled' :
                        this.props.error ? 'detail-error' : 'detail'}>
                        {this.props.detail ? this.props.detail : this.props.value.value ? this.props.value.value : null}
                    </div>
                    <Select
                        className='actual-dropdown'
                        classNamePrefix={this.props.error ? 'actual-dropdown-error' : 'actual-dropdown'}
                        isDisabled={this.props.disabled ? this.props.disabled : false}
                        value={this.props.value}
                        options={this.props.choices} 
                        onInputChange={(value: string, _a) => {
                            this.setState({value: value});
                        }}
                        noOptionsMessage={() => {
                            return (
                                this.props.choices.length === 0 ? (
                                    'Loading...'
                                ) : (
                                    this.state.value.length < 3 ? (
                                        'Enter at least 3 characters to search'
                                    ) : (
                                        'No options'
                                    )
                                )
                            )
                        }}
                        filterOption={(option: Option, rawInput: string) => {
                            const hasNoInput = (rawInput.length === 0);
                            const choicesAreShort = (this.props.choices.length <= 20);
                            const inputIsShort = (rawInput.length < 3);
                            const isInputMatch = (option.label.toLowerCase().includes(rawInput.toLowerCase()));
                            if (choicesAreShort) {
                                if (hasNoInput) {
                                    return true;
                                }
                                return isInputMatch;
                            } else {
                                if (!inputIsShort) {
                                    return isInputMatch;
                                }
                            }
                            return false;                            
                        }}
                        onChange={(choice: DropdownChoice) => {
                            this.props.onChange(choice);
                        }} />
                </div>
            </div>
        );
    }
}