import * as React from 'react';
import { TextareaAutosize } from '@material-ui/core';
import './component-style.scss';

interface IProps {
    placeholder?: string;
    value: string;
    defaultRows?: number;
    onChange: (value: string) => void;
}

export class InputArea extends React.Component<IProps> {

    shouldComponentUpdate(props: IProps, _state: any) {
        return (
            (this.props.placeholder !== props.placeholder) ||
            (this.props.value !== props.value) ||
            (this.props.defaultRows !== props.defaultRows)
        );
    }

    render(): JSX.Element {
        return (
            <div className='input-field-container'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : null}
                <TextareaAutosize 
                    className='text-field'
                    rows={this.props.defaultRows ? this.props.defaultRows : 4}
                    value={this.props.value}
                    onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) => {
                        this.props.onChange(event.target.value);
                    }}
                />
            </div>
        );
    }
}