import * as React from 'react';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { Grid, TableCell } from '@material-ui/core';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';

import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { CRUDTable } from 'Pages/CustomerCarePage/UpdateTicketPage/Components/CRUDTable';
import { DropdownChoice } from 'Pages/CustomerCarePage/Models';
import { CustomCheckbox } from 'Pages/CustomerCarePage/Components/CustomCheckbox';
import { DiagnosisData, PhysicianData, ProcedureData } from '../UpdateTicketProps';
import { CustomRadio } from 'Pages/CustomerCarePage/Components/CustomRadio';

interface IProps {
    diagnoses: DiagnosisData[];
    state: DiagnosisData;
    physicians: PhysicianData[];
    procedures: ProcedureData[];
    diagnosesList: DropdownChoice[];
    primaryDiagnosisIndex: number;
    refreshDiagnosisStateChange: () => void;
    setPrimaryDiagnosisIndex: (index: number) => void;
}

export class DiagnosesSection extends React.Component<IProps> {

    getFilteredICD10Codes(): DropdownChoice[] {
        let filteredIcd10: DropdownChoice[] = [];
        for (var i = 0; i < this.props.diagnosesList.length; i++) {
            let isSame = false;
            for (var j = 0; j < this.props.diagnoses.length; j++) {
                isSame = (
                    this.props.diagnoses[j].diagnosis.value === 
                    this.props.diagnosesList[i].value ? true : isSame);
            }
            if (!isSame) {
                filteredIcd10 = filteredIcd10.concat(this.props.diagnosesList[i]);
            }
        }
        return filteredIcd10;
    }

    onFormatTableHeader = (colIndex: number, value: any) => {
        return (
            colIndex === 0 || colIndex === 1 ? (
                <TableCell style={{minWidth: '200px', maxWidth: '200px'}}>{value}</TableCell>
            ) : colIndex === 3 ? (
                <TableCell style={{minWidth: '200px', maxWidth: '200px', textAlign: 'center'}}>{value}</TableCell>
            ) : (
                <TableCell style={{minWidth: '100px', maxWidth: '100px'}}>{value}</TableCell>
            )
        );
    }

    onFormatCRUDTable = (rowIndex: number, colIndex: number, value: any) => {
        return (
            colIndex === 0 ? (
                <div style={{minWidth: '200px', maxWidth: '200px'}}>
                    {value.value + " - " + value.label}
                </div>
            ) : colIndex === 2 ? (
            <div>{value ? 'Final' : 'Initial'}</div>
            ) : colIndex === 3 ? (
                <div style={{textAlign: 'center'}}>
                    <CustomRadio
                        disabled={!this.props.diagnoses[rowIndex].isFinal}
                        checkedCondition={this.props.primaryDiagnosisIndex === rowIndex}
                        onChange={() => {
                            this.props.setPrimaryDiagnosisIndex(rowIndex);
                        }} />
                </div>
            ) : (value)
        );
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Add Diagnosis</div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <DetailedDropdown 
                                placeholder='Diagnosis'
                                value={this.props.state.diagnosis}
                                // error={this.props.state.diagnosis.value === ''}
                                choices={this.getFilteredICD10Codes()}
                                onChange={(value: DropdownChoice) => {
                                    this.props.state.diagnosis = value;
                                    this.props.refreshDiagnosisStateChange();
                                }}/>
                        </Grid>
                        <Grid item xs={6}>
                            <InputField 
                                placeholder='Remarks'
                                value={this.props.state.remarks}
                                charLimit={100}
                                onChange={(value: string) => {
                                    this.props.state.remarks = value;
                                    this.props.refreshDiagnosisStateChange();
                                }}/>
                        </Grid>
                        <Grid item xs={6}>
                            <CustomCheckbox 
                                label='Final Diagnosis?'
                                value={this.props.state.isFinal}
                                onChange={(value: boolean) => {
                                    this.props.state.isFinal = value;
                                    this.props.refreshDiagnosisStateChange();
                                }}/>
                        </Grid>
                        
                    </Grid>
                    <div style={{paddingTop: '5px'}} />
                    {this.props.state.diagnosis.value !== '' ? (
                        <TextButton 
                            label='Add diagnosis' 
                            icon={faPlus}
                            onClick={() => {
                                var data = this.props.state;
                                const tempData = {
                                    diagnosis: data.diagnosis,
                                    remarks: data.remarks,
                                    isFinal: data.isFinal,
                                    isPrimary: true
                                }
                                this.props.diagnoses.unshift(tempData);
                                this.props.state.diagnosis = { value: '', label: '' };
                                this.props.state.remarks = '';
                                this.props.state.isFinal = false;
                                this.props.refreshDiagnosisStateChange();
                                this.props.setPrimaryDiagnosisIndex(this.props.primaryDiagnosisIndex + 1);
                            }}/>
                    ) : null}
                </div>
                <div className='sub-section' style={{overflowX: 'auto'}}>
                    <CRUDTable 
                        className='custom-table'
                        placeholder='DIAGNOSES'
                        
                        cols={['Diagnosis', 'Remarks', 'Type', 'Primary Diagnosis?']} 
                        rows={this.props.diagnoses}
                        onSaveEditRow={(index: number, data: any) => {
                            this.props.diagnoses[index] = data;
                            this.props.refreshDiagnosisStateChange();
                        }}
                        onDeleteRow={(index: number) => {
                            this.props.physicians.filter(physician => 
                                physician.diagnosis.label !== 
                                this.props.diagnoses[index].diagnosis.label);
                            this.props.diagnoses.splice(index, 1);
                            this.props.refreshDiagnosisStateChange();
                            this.props.setPrimaryDiagnosisIndex(
                                this.props.primaryDiagnosisIndex != index ?
                                this.props.primaryDiagnosisIndex - 1 : -1);
                        }}
                        onHeaderFormat={(colIndex: number, colName: string) => {
                            return this.onFormatTableHeader(colIndex, colName);
                        }} 
                        onFormat={(rowIndex: number, colIndex: number, value: any) => {
                            return this.onFormatCRUDTable(rowIndex, colIndex, value);
                        }}
                        editField={(colIndex: number, currentVal: any, onChange: any) => {
                            if (colIndex === 0) {
                                return (
                                    <DetailedDropdown
                                        choices={this.getFilteredICD10Codes()}
                                        value={currentVal}
                                        onChange={(value: DropdownChoice) => onChange(value)}/>
                                );
                            } else if (colIndex === 1) {
                                return (
                                    <InputField
                                        value={currentVal}
                                        onChange={(value: string) => onChange(value)}/>
                                );
                            } else if (colIndex === 2) {
                                return (
                                    <CustomCheckbox 
                                        label='Is Final?'
                                        value={currentVal}
                                        onChange={(value: boolean) => onChange(value)}/>
                                );
                            } else {
                                return (
                                    <div></div>
                                );
                            }
                        }} />
                </div>
                <div className='section' />
            </div>
        );
    }
}