
let backendUrl = process.env.REACT_APP_BASE_URL;

export class API {

    static getUrl() {
        return process.env.REACT_APP_BASE_URL;
    }

    static async get(url: string, success: Function, fail?: Function) {
        fetch(backendUrl + url, {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' }
        })
        .then(response => response.json())
        .then(data => success(data))
        .catch(error => fail ? fail(error) : {});
    }

    static async post(url: string, 
                      body: any,
                      success: Function, 
                      fail?: Function,
                      customHeader: any = { 'Content-Type': 'application/json' }) {
        fetch(backendUrl + url, {
            method: 'POST',
            headers: customHeader,
            body: body
        })
        .then(response => response.json())
        .then(data => success(data))
        .catch(error => fail ? fail(error) : {});
    }

    static async postFormData(url: string, body: any, success: Function, fail?: Function) {
        fetch(backendUrl + url, {
            method: 'POST',
            body: body
        })
        .then(response => response.json())
        .then(data => success(data))
        .catch(error => fail ? fail(error) : {});
    }

    static async put(url: string, body: any, success: Function, fail?: Function) {
        fetch(backendUrl + url, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
        .then(response => response)
        .then(data => success(data))
        .catch(error => fail ? fail(error) : {});
    }

    static async patch(url: string, body: any, success: Function, fail?: Function) {
        fetch(backendUrl + url, {
            method: 'PATCH',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
        .then(response => response)
        .then(data => success(data))
        .catch(error => fail ? fail(error) : {});
    }

    static async delete(url: string, body: any, success: Function, fail?: Function) {
        fetch(backendUrl + url, {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(body)
        })
        .then(response => response)
        .then(data => success(data))
        .catch(error => fail ? fail(error) : {});
    }
}