import * as React from 'react';
import { TableCell, TableRow } from '@material-ui/core';
import { ClientData, LogData } from 'Pages/CustomerCarePage/Models';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { API } from 'Pages/CustomerCarePage/API';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { faSort } from '@fortawesome/free-solid-svg-icons';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';

interface IProps {
    client: ClientData;
}

interface IStates {
    logs?: LogData[];
}

export class AuditLogsSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {

        };
    }

    componentDidMount() {
        this.getAuditLogs();
    }

    getAuditLogs = () => {
        const filter = {
            where: {
                client_id: this.props.client.id
            }
        };
        API.get(`audit-logs?filter=${JSON.stringify(filter)}`,
            (response) => {
                console.log(response);
                this.setState({logs: Payloads.getAuditLogs(response.audit_logs)});
            },
            (error) => {
                console.log(error);
            });
    }

    render() : JSX.Element {

        return (
            <div className='sub-section'>
                {this.state.logs ? (
                    <div style={{overflowX: 'auto'}}>
                        <CustomTable
                            rows={this.state.logs}
                            renderHeader={() => this.renderTableHeader()}
                            rowsPerPageOptions={[10, 25, 50]}
                            renderRow={(rowData: any, index: number) => 
                                this.renderTableRow(rowData, index)} />
                    </div>
                ) : (
                    <Loading height='10rem' />
                )}
               
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            {this.renderHeaderColumn('fiterName', 'Activity ID')}
            {this.renderHeaderColumn('filterBenefits', 'Activity')}
            {this.renderHeaderColumn('filterPrincipals', 'User')}
            {this.renderHeaderColumn('filterDependents', 'Date')}
            {this.renderHeaderColumn('filterDateCreated', 'Time')}
            </>
        );
    }

    renderTableRow(log: LogData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{log.activityId}</TableCell>
                <TableCell>{log.name}</TableCell>
                <TableCell>{log.user}</TableCell>
                <TableCell>{log.date}</TableCell>
                <TableCell>{log.time}</TableCell>
            </TableRow>
        );
    }

    renderHeaderColumn(attrName: string, headerText: string, width?: string): JSX.Element {
        return (
            <TableCell style={{minWidth: width ? width : '150px'}}>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            // this.sortFacilities(attrName, true);
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        // @ts-ignore
                        this.setState({[attrName]: value});
                    }} />
            </TableCell>
        );
    }
}