import * as React from 'react';
import './component-style.scss';
import { CircularProgress } from "@material-ui/core";

interface IProps {
    height?: string;
    color?: string;
    size?: number;
}

export class Loading extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <div 
                style={{
                    textAlign: 'center', 
                    paddingTop: this.props.height ? this.props.height : undefined}}>
                <CircularProgress 
                    size={this.props.size}
                    style={{
                        color: this.props.color ? this.props.color : undefined}} />
            </div>
            
        );
    }
}