import * as React from 'react';
import { Grid } from '@material-ui/core';
import { ClientData } from 'Pages/CustomerCarePage/Models';

interface IProps {
    client: ClientData;
}

interface IStates {
    
}

export class BasicInformationSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {

        };
    }

    render() : JSX.Element {
        const client = this.props.client;

        const yearsAndCount = client.years ? `${new Date().getUTCFullYear() - client.years} (${client.years})` : '---';
        const brokerName = client.salesChannel ? client.salesChannel.contactName : '---';
        const brokerCompany = client.salesChannel ? client.salesChannel.company : '---';

        return (
            <Grid container spacing={2}>
                {this.renderItem('CORPORATE ACCOUNT NO.', client.corporateAccountNo, 12, 'header-1')}
                {this.renderItem('REGISTERED COMPANY NAME', client.registeredName)}
                {this.renderItem('TRADE COMPANY NAME', client.brandName)}
                {this.renderItem('INDUSTRY', client.industry || '---')}
                {this.renderItem('OWNWESHIP', client.ownership || '---')}
                {this.renderItem('CORPORATE TIN', client.tinNo || '---')}
                {this.renderItem('SEC REGISTRATION NO.', client.secRegNo || '---')}
                {this.renderItem('E-VAT NO.', client.evatNo || '---')}
                {this.renderItem('ESTABLISHED (YEARS)', yearsAndCount)}
                {this.renderItem('AUTHORIZED SIGNATORY', client.authSignatory)}
                {this.renderItem('BROKER/BDO', brokerName)}
                {this.renderItem('BROKER COMPANY', brokerCompany)}
                {this.renderItem('PREVIOUS HMO', client.prevHMOProvider)}
            </Grid>
        );
    }

    renderItem(label: string, value?: string | number, xs?: any, className?: string): JSX.Element {
        return (
            <Grid item className='sub-section' xs={xs ? xs : 3} style={{padding: '16px 8px'}}>
                {label.length > 0 ? (
                    <div className='light-text'>{label}</div>
                ) : null}
                {value ? (
                    <div className={className ? className : 'header-2'}>{value}</div>
                ) : null}
            </Grid>
        )
    }
}