import * as React from 'react';
import { API } from 'Pages/CustomerCarePage/API';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { Grid } from '@material-ui/core';
import { TicketData, AvailmentDetails, ConfinementDetails } from 'Pages/CustomerCarePage/Models';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';

interface IProps {
    ticketId: string;
    onCloseButtonClicked: () => void;
}

interface IStates {
    ticket?: TicketData;
    availment?: AvailmentDetails;
    confinement?: ConfinementDetails;
}

export class TicketInfoModal extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            
        };
    }

    componentDidMount() {
        console.log(this.props.ticketId);

        API.get('customer-care/tickets/' + this.props.ticketId, 
            (response) => {
                const ticketCategory = response.ticket_category;
                const availmentDetails = ticketCategory.availment_details;
                const confinementDetails = ticketCategory.confinement_details;

                this.setState({ticket: Payloads.getTicketData(response)});
                
                if (availmentDetails) {
                    const providerData = availmentDetails.provider.split(' - ');
                    this.setState({availment: {
                        provider: { value: providerData[0], label: providerData[1] },
                        diagnoses: availmentDetails.availments_list.map(diagnosis => {
                            return {
                                icd10Code: { value: diagnosis.code, label: diagnosis.name },
                                remarks: diagnosis.remarks,
                                isPrimary: diagnosis.is_primary,
                                isFinal: diagnosis.is_final,
                                doctors: diagnosis.doctors.map(doctor => {
                                    return {
                                        id: doctor.id,
                                        name: doctor.name,
                                        specialization: doctor.specialization,
                                        fee: doctor.fee,
                                        noOfVisits: doctor.no_of_visits,
                                        remarks: doctor.remarks
                                    }
                                }),
                                procedures: diagnosis.procedures.map(procedure => {
                                    return {
                                        cptCode: { value: procedure.code, label: procedure.name },
                                        isPHIC: procedure.phic,
                                        amount: parseInt(procedure.amount),
                                        notes: procedure.notes,
                                        assessmentType: procedure.assessment_type,
                                        assessmentRemarks: procedure.assessment_remarks
                                    }
                                })
                            }
                        })
                    }});
                }

                if (confinementDetails) {
                    const providerData = availmentDetails.provider.split(' - ');
                    this.setState({availment: {
                        provider: { value: providerData[0], label: providerData[1] },
                        diagnoses: availmentDetails.availments_list.map(diagnosis => {
                            return {
                                icd10Code: { value: diagnosis.code, label: diagnosis.name },
                                remarks: diagnosis.remarks,
                                isPrimary: diagnosis.is_primary,
                                isFinal: diagnosis.is_final,
                                doctors: diagnosis.doctors.map(doctor => {
                                    return {
                                        id: doctor.id,
                                        name: doctor.name,
                                        specialization: doctor.specialization,
                                        fee: doctor.fee,
                                        noOfVisits: doctor.no_of_visits,
                                        remarks: doctor.remarks
                                    }
                                }),
                                procedures: diagnosis.procedures.map(procedure => {
                                    return {
                                        cptCode: { value: procedure.code, label: procedure.name },
                                        isPHIC: procedure.phic,
                                        amount: parseInt(procedure.amount),
                                        notes: procedure.notes,
                                        assessmentType: procedure.assessment_type,
                                        assessmentRemarks: procedure.assessment_remarks
                                    }
                                })
                            }
                        })
                    }});
                }
            },
            (error) => {
                console.log(error);
            });
    }

    getTotalAailmentProcedureCost(): number {
        let total = 0;

        if (this.state.availment) {
            this.state.availment.diagnoses.map(diagnosis => 
                diagnosis.procedures.map(procedure => 
                    total += procedure.amount
                )
            )
        }
        
        return total;
    }

    render(): JSX.Element {
        return (
            <div className='ticket-modal section'>
                <div style={{float: 'right', marginTop: '-1rem', fontSize: '1rem'}}>
                    <IconButton 
                        icon={faTimes} 
                        onClick={() => {
                            this.props.onCloseButtonClicked();
                        }} />
                </div>
                {this.state.ticket ? (
                    this.renderTicketInfo(this.state.ticket)
                ) : null}
            </div>
        )
    }

    renderTicketInfo(ticket: TicketData): JSX.Element {
        return (
            <>
            <div className='sub-sections'>
                <div className='header-1'>{`Ticket ${ticket.ticketNo}`}</div>
                <div className='text'>{ticket.status + ' - ' + ticket.urgency + ', Due on May 23, 2019*'}</div>
            </div>
            <div className='sub-section'>
                {/* <div className='text'>{name}</div>
                <div className='text'>{"Happytime Technology Inc."}</div>
                <div className='text'>{'Rank and File - Principal'}</div> */}
                <Grid container>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>TICKET TYPE</div>
                        <div className='small-text'>{ticket.ticketType}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>SPECIFIC TYPE</div>
                        <div className='small-text'>{ticket.specificType}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>RECEIVED VIA</div>
                        <div className='small-text'>{ticket.ticketChannel}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>CALL TRANSACTION NO.</div>
                        <div className='small-text'>{ticket.transactionNo}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>RECEIVED FROM</div>
                        <div className='small-text'>{'??? ' + '(' + ticket.ticketSource + ')'}</div>
                    </Grid>
                    <Grid item className='sub-section' xs={6}>
                        <div className='light-text'>NUMBER / EMAIL</div>
                        <div className='small-text'>{ticket.contactNo}</div>
                    </Grid>
                </Grid>
                <div className='sub-section'>
                    <div className='light-text'>ASSIGNED TO</div>
                    <div className='small-text'>{ticket.assignee.name}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DIAGNOSIS — PROCEDURES</div>
                    {this.state.availment ? (
                        <Grid container spacing={2} className='sub-section'>
                            {this.state.availment.diagnoses.map((diagnosis, index) => {
                                return (
                                    <>
                                    <Grid item xs={12} className='header-2'>
                                        {(index + 1) + '. ' +
                                         diagnosis.icd10Code.value + ' - ' + 
                                         diagnosis.icd10Code.label}
                                    </Grid>
                                    {diagnosis.procedures.map((procedure, index2) => {
                                        return (
                                            <>
                                            <Grid item xs={1} />
                                            <Grid item xs={11} className='small-text'>
                                                {(index2 + 10).toString(36) + '. ' + procedure.cptCode.label}
                                            </Grid>
                                            </>
                                        );
                                    })}
                                    </>
                                );
                            })}
                        </Grid>
                    ) : null}
                    {/* <div className='sub-section'> */}
                    <div style={{paddingTop: '0.5rem'}} />
                    <div className='header-2'>{'TOTAL: PHP ' + GlobalFunction.formatNumber(this.getTotalAailmentProcedureCost(), true)}</div>
                {/* </div> */}
                </div>
                
                <div className='sub-section'>
                    <div className='light-text'>CREATED BY</div>
                    <div className='small-text'>{ticket.createdBy.name}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME CREATED</div>
                    <div className='small-text'>{GlobalFunction.toDateAndTime(ticket.dateCreated)}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME LAST UPDATED</div>
                    <div className='small-text'>{GlobalFunction.toDateAndTime(ticket.dateUpdated)}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME LAST ESCALATED</div>
                    <div className='small-text'>{"May 14, 2019 — 09:33:00 AM*"}</div>
                </div>
                {/* <div className='sub-section'> */}
                    
                {/* </div> */}
            </div>
            <TextButton
                        label='View More'
                        onClick={() => {

                        }} />
            </>
        )
    }
}