import * as React from 'react';
import { ComplexTable } from 'Pages/CustomerCarePage/Components/ComplexTable';
import { Grid,Link } from '@material-ui/core';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { ProcedureData, AvailmentDetails, DiagnosisData } from 'Pages/CustomerCarePage/Models';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';
import { CustomRadio } from 'Pages/CustomerCarePage/Components/CustomRadio';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faInfoCircle
  } from '@fortawesome/free-solid-svg-icons';
import {AdjudicationModal} from './AdjudicationStepModal';
interface IProps {
    availment: AvailmentDetails;
    refreshAvailmentState: () => void;
}

interface IStates {
    isModalOpen: boolean,
}
export class ApproveAvailmentSection extends React.Component<IProps,IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            isModalOpen: false,
        };
    }

    getProceduresCost = (diagnosis: DiagnosisData) => {
        let total = 0;
        diagnosis.procedures.map(procedure => {
            total += procedure.amount;
        });
        return total;
    }

    getApprovedAvailmentsCost = () => {
        let total = 0;
        this.props.availment.diagnoses.map(diagnosis => {
            diagnosis.procedures.map(procedure => {
                total += (procedure.assessmentType === 'Approved' ? procedure.amount : 0);
            });
        });
        return total;
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Approve or Disapprove Availments</div>
                <div className='sub-section'>
                    <div className='light-text'>PROVIDER</div>
                    <div className='small-text'>
                        {this.props.availment.provider.value + ' - ' + this.props.availment.provider.label}
                    </div>
                    <div style={{float: 'right'}}>
                        <span className='small-text'>TOTAL AMOUNT</span>
                        <span style={{marginLeft: '1rem'}} />
                        <span className='header-2'>
                            {'PHP ' + GlobalFunction.formatNumber(this.getApprovedAvailmentsCost(), true)}
                        </span>
                    </div>
                </div>
                <div className='sub-section'>
                    <ComplexTable 
                        data={this.props.availment.diagnoses}
                        renderHeader={() => this.renderHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderRow(rowData, index)} />
                </div>
                <div className='section' />
            </div>
        );
    }

    onModalClose = ()=>{
        this.setState({isModalOpen:false})
    }
    
    onModalOpen = () =>{
        this.setState({isModalOpen:true})
    }

    renderHeader(): JSX.Element {
        const headerNameStyle = {
            margin: 0,
            padding: '12px 0 12px 0'
        };

        return (
            <>
                <Grid item xs={4}>
                    <div className='header-2' style={headerNameStyle}>Diagnosis</div>
                </Grid>
                <Grid item xs={3}>
                    <div className='header-2' style={headerNameStyle}>Doctor</div>
                </Grid>
                <Grid item xs={2}>
                    <div className='header-2' style={headerNameStyle}>Professional Fee</div>
                </Grid>
                <Grid item xs={3}>
                    <div className='header-2' style={{...headerNameStyle, textAlign: 'right'}}>
                        Total Amt. of Procedures
                    </div>
                </Grid>
            </>
        );
    }

    renderRow(diagnosis: DiagnosisData, _index: number): JSX.Element {
        return (
            <>
                <Grid className='small-section' container>
                    <Grid item xs={4}>
                        {diagnosis.icd10Code.value + ' - ' + diagnosis.icd10Code.label}
                    </Grid>
                    <Grid item xs={3}>
                        {diagnosis.doctors[0].name}
                    </Grid>
                    <Grid item xs={2}>
                        {'PHP ' + GlobalFunction.formatNumber(diagnosis.doctors[0].fee, true)}
                    </Grid>
                    <Grid item xs={3} style={{fontWeight: 'bold', textAlign: 'right'}}>
                        {'PHP ' + GlobalFunction.formatNumber(this.getProceduresCost(diagnosis), true)}
                    </Grid>
                </Grid>
                <div className='line-border' />
                {diagnosis.procedures.map((procedure, index) => {
                    return this.renderProcedure(procedure, index);
                })}
            </>
        );
    }

    renderProcedure(procedure: ProcedureData, index: number): JSX.Element {
        return (
            <>
            <div className='line-border' />
            <div className='section' >
                
                <Grid container>
                    <Grid item xs={4}>
                        <div>
                            <Grid container>
                                <Grid item xs={2} style={{padding: '0 0.5rem', marginTop: '-3px'}}>
                                    <div className='number-item'>{index + 1}</div>
                                </Grid>
                                <Grid item xs={10} className='header-2'>
                                    {procedure.cptCode.value + ' - ' + procedure.cptCode.label}
                                </Grid>
                            </Grid>
                        </div>
                    </Grid>
                    
                </Grid>
                <Grid container spacing={1}>
                    <Grid item xs={4}>
                        <div>
                            <Grid container>
                                <Grid item xs={2} />
                                <Grid item xs={10}>
                                    <div className='sub-section'>
                                        <div className='light-text'>AMOUNT</div>
                                        <div className='small-text'>{'PHP ' + GlobalFunction.formatNumber(procedure.amount, true)}</div>
                                    </div>
                                    <div className='sub-section'>
                                        <div className='light-text'>PHILHEALTH REQUIRED?</div>
                                        <div className='small-text'>{procedure.isPHIC ? 'Yes' : 'No'}</div>
                                    </div>
                                    <div className='sub-section'>
                                        <div className='light-text'>DATE OF AVAILMENT</div>
                                        <div className='small-text'>{GlobalFunction.toCommonDateString(procedure.dateAdded)}</div>
                                    </div>
                                </Grid>
                            </Grid>
                        </div>
                    </Grid>
                    <Grid item xs={3}>
                        <div className='sub-section'>
                            <div className='light-text'>SYSTEM ASSESSMENT</div>
                            <div className='small-text'>{'Approve *'}
                                <Link onClick={this.onModalOpen}><FontAwesomeIcon style={{color:'#C3C3C3'}} icon={faInfoCircle} /></Link></div>
                        </div>
                        <div className='sub-section'>
                            <div className='light-text'>REMAINING BALANCE</div>
                            <div className='small-text'>{'PHP ' + 0 + '.00 *'}</div>
                        </div>
                        <div className='sub-section'>
                            <div className='light-text'>CLAUSE</div>
                            <div className='small-text'>{'Laboratory tests, x-rays, and other diagnostic examinations prescribed by the INTELLICARE physician*'}</div>
                        </div>
                    </Grid>
                    <Grid item xs={2}>
                        <div className='sub-section'>
                            <div className='light-text'>Choose Assessment</div>
                            <CustomRadio 
                                label='Approve'
                                checkedCondition={procedure.assessmentType === 'Approved'}
                                onChange={() => {
                                    procedure.assessmentType = 'Approved';
                                    this.props.refreshAvailmentState();
                                }} />
                            {procedure.assessmentType === 'Approved' ? (
                                <NumberField
                                    value={procedure.approvedAmount}
                                    placeholder='Partial (optional)'
                                    isFloat
                                    onChange={(value: number) => {
                                        procedure.approvedAmount = value;
                                        this.props.refreshAvailmentState();
                                    }} />
                            ) : null}
                            <CustomRadio 
                                label='Disapprove'
                                checkedCondition={procedure.assessmentType === 'Disapproved'}
                                onChange={() => {
                                    procedure.assessmentType = 'Disapproved';
                                    this.props.refreshAvailmentState();
                                }} />
                            <CustomRadio 
                                label='Pending'
                                checkedCondition={procedure.assessmentType === 'Pending'}
                                onChange={() => {
                                    procedure.assessmentType = 'Pending';
                                    this.props.refreshAvailmentState();
                                }} />
                        </div>
                    </Grid>
                    <Grid item xs={3}>
                        <div className='sub-section'>
                            <div className='light-text'>Remarks</div>
                            <InputArea
                                value={procedure.assessmentRemarks}
                                defaultRows={5}
                                onChange={(value: string) => {
                                    procedure.assessmentRemarks = value;
                                    this.props.refreshAvailmentState();
                                }} />
                        </div>
                    </Grid>
                </Grid>
                <AdjudicationModal
                    isModalOpen={this.state.isModalOpen}
                    onClose={this.onModalClose}
                />
            </div>
            </>
        );
    }
}