import * as React from 'react';
import { Table, TableBody, TableHead, TableRow, TablePagination, TableCell } from '@material-ui/core';
import './component-style.scss';
import { LabelDisplayedRowsArgs } from '@material-ui/core/TablePagination';
import { DropdownChoice } from '../Models';

interface IProps {
    className?: string;
    paginationClass?: string;

    /* Normal Mode */
    rows: any[];
    renderHeader: () => JSX.Element;
    renderRow: (rowData: any, index: number) => JSX.Element;
    noRowsMessage?: string | JSX.Element;

    /* Simple Mode - Automatically handles styling, so you can focus on data. */
    columns?: DropdownChoice[]; // value: attribute, label: the title appearing on the header
    canFilter?: boolean;
    canSort?: boolean;

    stickyHeader?: boolean;
    canOverflow?: boolean;
    rowsPerPageOptions?: number[];
    labelRowsPerPage?: string;
    onChangeInfo?: (info: LabelDisplayedRowsArgs) => string;

    onChangeState?: (page: number, rowsPerPage: number) => void;
}

interface IStates {
    page: number;
    rowsPerPage: number;
    
    filterData: any;
}

export class CustomTable extends React.PureComponent<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            page: 0,
            rowsPerPage: 0,

            filterData: {}
        }
    }

    componentDidMount() {
        if (this.props.rowsPerPageOptions) {
            this.setState({rowsPerPage: this.props.rowsPerPageOptions[0]});
        }
    }

    handleChangePage = (_event: unknown, page: number) => {
        this.setState({page: page});
    }
    
    handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({
            rowsPerPage: parseInt(event.target.value, 10),
            page: 0
        });
    }

    resetPage = () => {
        this.setState({page: 0});
    }

    getSlicedData = () => {
        if (this.props.rowsPerPageOptions) {
            const startingItem = this.state.page * this.state.rowsPerPage;
            return this.props.rows.slice(startingItem, startingItem + this.state.rowsPerPage);
        } else {
            return this.props.rows;
        }
    }

    componentWillUpdate(_props: IProps, state: IStates) {
        if (this.state.page != state.page ||
            this.state.rowsPerPage != state.rowsPerPage) {
            if (this.props.onChangeState) {
                this.props.onChangeState(state.page, state.rowsPerPage);
            }
        }
    }

    render(): JSX.Element {
        return (
            <>
            <div style={{overflow: this.props.canOverflow ? 'auto' : 'none'}}>
                <Table 
                    className={`${(this.props.className ? this.props.className : '')} table`} 
                    stickyHeader={this.props.stickyHeader ? this.props.stickyHeader : false}>
                    <TableHead>
                        {this.props.columns ? (
                            <TableRow>
                                {this.props.columns.map(column => {
                                    return <TableCell>{column.label}</TableCell>
                                })}
                            </TableRow>
                        ) : this.props.renderHeader ? (
                            <TableRow>
                                {this.props.renderHeader()}
                            </TableRow>
                        ) : null}
                    </TableHead>
                    <TableBody>
                        {this.props.renderRow ? (
                            this.props.rows.length > 0 ? (
                                this.getSlicedData().map((row, j) => {
                                    return this.props.renderRow(row, j)
                                })  
                            ) : (
                                <TableRow>
                                    <TableCell colSpan={999} style={{textAlign: 'center'}}>
                                        {this.props.noRowsMessage ? this.props.noRowsMessage : 'No data found'}
                                    </TableCell>
                                </TableRow>
                            )
                        ) : null}
                    </TableBody>
                </Table>
            </div>
            {this.props.rowsPerPageOptions ? (
                <TablePagination
                    className={`${(this.props.paginationClass ? this.props.paginationClass : '')} pagination MuiTablePagination-root`}
                    rowsPerPageOptions={this.props.rowsPerPageOptions}
                    count={this.props.rows.length}
                    rowsPerPage={this.state.rowsPerPage}
                    page={this.state.page}
                    labelDisplayedRows={(info: LabelDisplayedRowsArgs) => {
                        return (
                            this.props.onChangeInfo ? (
                                this.props.onChangeInfo(info)
                            ) : (
                                `${info.from}-${info.to} of ${info.count}`
                            ));
                    }}
                    labelRowsPerPage={
                        this.props.labelRowsPerPage ? (
                            this.props.labelRowsPerPage
                        ) : (
                            'Rows per page:'
                        )}
                    onChangePage={(_event: unknown, page: number) => {
                        this.setState({page: page});
                    }}
                    onChangeRowsPerPage={(event: React.ChangeEvent<HTMLInputElement>) => {
                        this.setState({
                            rowsPerPage: parseInt(event.target.value, 10),
                            page: 0
                        });
                    }} />
            ) : null}
            </>
        );
    }
}