
import * as React from 'react';
import './component-style.scss';
import { Chip } from '@material-ui/core';

interface IProps {
    label: string;
    value: string;
    onDelete: (value: string) => void;
}

export class CustomChip extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <Chip 
                className='chip'
                label={this.props.label} 
                onDelete={() => {this.props.onDelete(this.props.value)}} />
        );
    }
}