import * as React from 'react';
import { ClientData } from 'Pages/CustomerCarePage/Models';
import { CustomCheckbox } from 'Pages/CustomerCarePage/Components/CustomCheckbox';

interface IProps {
    client: ClientData;
}

export class AvailmentSection extends React.Component<IProps> {

    render() : JSX.Element {
        const availment = this.props.client.servicingInstructions ? 
                          this.props.client.servicingInstructions.availment : 
                          undefined;

        return (
            <div className='sub-section'>
                <CustomCheckbox
                    label='Inform member of copayment / coinsurance share'
                    disabled
                    value={availment && availment.includes('inform member of copayment') ? true : false}
                    onChange={(_value: boolean) => {}} />
            </div>
        );
    }
}