import * as React from 'react';
// import { InputBase } from '@material-ui/core';
import { IconProp } from '@fortawesome/fontawesome-svg-core';
import './component-style.scss';

interface IProps {
    value: string;
    onChange: (value: string) => void;

    placeholder?: string;
    error?: boolean;
    icon?: IconProp;
    disabled?: boolean;
    charLimit?: number;
}

export class InputField extends React.Component<IProps> {

    

    shouldComponentUpdate(props: IProps, _state: any) {
        return (
            (this.props.placeholder !== props.placeholder) ||
            (this.props.value !== props.value) ||
            (this.props.error !== props.error)
        );
    }

    render(): JSX.Element {
        const self = this;
        const dynamicStyle = {
            borderColor: (self.props.error ? '#E53935' : undefined)
        };

        return (
            <div className='input-field-container'>
                {this.props.placeholder ? (
                    <div className={this.props.disabled ? 'text-disabled text' : 'text'}>
                        {this.props.placeholder}
                    </div>
                ) : null}
                <input type='text' 
                    disabled={this.props.disabled}
                    className={this.props.disabled ? 'text-field-disabled text-field' : 'text-field'}
                    style={dynamicStyle}
                    // fullWidth 
                    value={this.props.value}
                    onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                        const condition = 
                            this.props.charLimit ? (
                                event.target.value.length <= this.props.charLimit 
                            ) : (true) ;
                            
                        if (condition) {
                            this.props.onChange(event.target.value);
                        }
                    }} />
                {this.props.charLimit ? (
                    <>
                    <div className='text' />
                    <div 
                        className={this.props.disabled ? 'text-disabled text' : 'text'}
                        style={{textAlign: 'right'}}>
                            {`${this.props.value.length}/${this.props.charLimit}`}
                    </div>
                    </>
                ) : null}
                
            
            </div>
        );
    }
}