import * as React from 'react';
import { CustomTable } from '../../Components/CustomTable';
import { TableCell, TableRow, Grid, Snackbar } from '@material-ui/core';
import { InputField } from '../../Components/InputField';
import { TextButton } from '../../Components/TextButton';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { CommentData } from 'Pages/CustomerCarePage/Models';
import { GlobalFunction, TimeOption } from 'Pages/CustomerCarePage/GlobalFunction';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { API } from 'Pages/CustomerCarePage/API';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';

interface IProps {
    ticketId: string;
    comments?: CommentData[];

    refreshComments: () => void;
}

interface IStates {
    comment: string;
    timeNow: number;

    snackbarMessage: string | null;
    isSending: boolean;
}

export class CommentSection extends React.Component<IProps, IStates> {

    interval?: NodeJS.Timeout;

    constructor(props: IProps) {
        super(props);

        this.state = {
            comment: '',
            timeNow: 0,

            snackbarMessage: null,
            isSending: false
        };
    }

    componentDidMount() {
        this.interval = setInterval(() => this.setState({ timeNow: Date.now() }), 5000);
    }

    onAddComment = () => {
        if (this.props.comments) {
            this.props.comments.unshift({
                sender: `${localStorage['first_name']} ${localStorage['last_name']}`, 
                message: this.state.comment, 
                date: new Date()
            });
            this.props.refreshComments();
            this.setState({isSending: true});

            const payload = Payloads.getPatchCommentPayload(this.props.comments);

            API.patch(`customer-care/tickets/${this.props.ticketId}`, payload,
                (patchTicketResponse) => {
                    console.log('Patch Response: ', patchTicketResponse);
                    if (patchTicketResponse.status < 400) {
                        this.setState({comment: '', snackbarMessage: 'You added new comment.'});
                    } else {
                        this.setState({comment: '', snackbarMessage: 'Failed to add comment.'});
                        if (this.props.comments) {
                            this.props.comments.shift();
                            this.props.refreshComments();
                        }
                    }
                    this.setState({isSending: false});
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        comment: '', 
                        isSending: false,
                        snackbarMessage: 'Failed to add comment.'
                    });
                    if (this.props.comments) {
                        this.props.comments.shift();
                        this.props.refreshComments();
                    }
                }
            );
        }
    }



    render(): JSX.Element {
        return (
            <div className='section' style={{width: '100%'}}>

                <Snackbar
			        anchorOrigin={{vertical: 'top', horizontal: 'right'}}
			        open={this.state.snackbarMessage ? true : false}
			        autoHideDuration={6000}
			        onClose={() => {
                        this.setState({snackbarMessage: null});
                    }}
                    message={this.state.snackbarMessage} />

                {this.props.comments ? (
                    <>
                    <div style={{paddingTop: '2.5rem'}} />
                    <div className='header-1'>{`Comments (${this.props.comments.length})`}</div>
                    <div className='sub-section'>
                        <CustomTable 
                            rows={this.props.comments}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderTableRow(rowData, index)} />
                    </div>
                    </>
                ) : (
                    <Loading height='10rem' />
                )}
                <div className='section' />
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            <TableCell>
                <InputField
                    value={this.state.comment}
                    innerPlaceholder='Type in a comment'
                    onChange={(value: string) => {
                        this.setState({comment: value});
                    }}
                    onKeyDown={(key: string) => {
                        if (key === 'Enter' && !this.state.isSending && this.state.comment !== '') {
                            this.onAddComment();
                        }
                    }} />
            </TableCell>
            {this.state.comment && !this.state.isSending ? (
                <TableCell style={{width: '150px', textAlign: 'center'}}>
                    <TextButton 
                        icon={faPlus} 
                        label='Add comment'
                        onClick={() => {
                            this.onAddComment();
                        }} />
                </TableCell>
            ) : this.state.isSending ? (
                <TableCell style={{width: '150px', textAlign: 'center'}}>
                    <Loading height='none' size={16} />
                </TableCell>
            ) : null}
            
            </>
        );
    }

    renderTableRow(comment: CommentData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        const timeOption: TimeOption = {
            dateTimeDivider: ' at ',
            showDayFromWeek: true
        };
        const timeDiff = GlobalFunction.getTimeDeffirence(comment.date, new Date());

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell colSpan={2}>
                    <Grid container spacing={1}>
                        <Grid item xs={7} className='header-2'>
                            {comment.sender}
                        </Grid>
                        <Grid item xs={5} className='light-text' style={{textAlign: 'right'}}>
                            {(timeDiff.length > 0 ? timeDiff + ' ago ' : ' ') + 
                            `(${GlobalFunction.toDateAndTime(comment.date, timeOption)})`}
                        </Grid>
                        <Grid item xs={12} className='text'>
                            {comment.message}
                        </Grid>
                    </Grid>
                </TableCell>
            </TableRow>
        );
    }
}


