import * as React from 'react';
import { Grid } from '@material-ui/core';
import { ClientData } from 'Pages/CustomerCarePage/Models';
import { API } from 'Pages/CustomerCarePage/API';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { BasicInformationSection } from './BasicInformationSection';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { ContactsAndBranchesSection } from './ContactsAndBranchesSection';
import { ServicingInstructionsSection } from './ServicingInstructionsSection/ServicingInstructionsSection';
import { HMOInformationSection } from './HMOInformationSection';
import { MembersSection } from './MembersSection';
import { HistorySection } from './HistorySection';
import { DocumentsSection } from './DocumentsSection';
import { AuditLogsSection } from './AuditLogsSection';
import { MemberFieldsSection } from './MemberFieldsSection';
import { TextTab } from 'Pages/CustomerCarePage/Components/TextTab';

interface IProps {
    history: any;
    clientId: string;
}

interface IStates {
    client?: ClientData;
    selectedTab: number;
}

export class ClientProfileSection extends React.Component<IProps, IStates> {

    informationTabs = [
        { value: 1, label: 'Basic Information' },
        { value: 2, label: 'Contracts/Branches' },
        { value: 3, label: 'Member Fields' },
        { value: 4, label: 'Servicing Instructions' }
    ];

    accountTabs = [
        { value: 5, label: 'HMO Information' },
        { value: 6, label: 'Members' },
        { value: 7, label: 'History' },
        { value: 8, label: 'Documents' }
    ];

    activitiesTabs = [
        { value: 9, label: 'Audit Logs' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 1
        };
    }

    componentDidMount() {
        API.get(`clients/account/hmo-info/${this.props.clientId}`,
            (response) => {
                console.log(response);
                this.setState({client: 
                    Payloads.getClientData(
                        response.client, 
                        response.benefit_plan_tree,
                        response.plan_types)});
            },
            (error) => {
                console.log(error);
            });
    }

    render() : JSX.Element {
        return (
            <div className='section' style={{width: '100%'}}>
                <div style={{paddingTop: '2.5rem'}} />
                <Grid container className='sub-section'>
                    {this.renderSideBar()}
                    {this.renderMainContent()}
                </Grid>
                <div className='section' />
            </div>
        );
    }

    renderSideBar(): JSX.Element {
        const rightBorderStyle = {
            borderRight: '#3C394A22 solid 1px'
        }
        return (
            <Grid item xs={2} style={rightBorderStyle} spacing={5}>
                {this.renderSideTabs('INFORMATION', this.informationTabs)}
                {this.renderSideTabs('ACCOUNT', this.accountTabs)}
                {this.renderSideTabs('ACTIVITIES', this.activitiesTabs)}
            </Grid>
        );
    }

    renderSideTabs(label: string, tabList: any[]): JSX.Element {
        return (
            <div className='sub-section'>
                <div className='light-text'>{label}</div>
                <TextTab
                    tabList={tabList}
                    selectedTab={this.state.selectedTab}
                    orientation='vertical'
                    tabLabel={(index: number) => {
                        return (
                            <div style={{width: '100%'}}>
                                <div className='header-3' style={{margin: '6px 0'}}>
                                    {tabList[index].label}
                                </div>
                            </div>
                        );
                    }}
                    setSelectedTab={(value: number) => {
                        this.setState({selectedTab: value});
                    }} />
            </div>
        );
    }

    renderMainContent(): JSX.Element {
        return (
            <Grid item xs={10} className='small-section'>
                {this.state.client ? (
                    this.state.selectedTab == 1 ? (
                        <BasicInformationSection client={this.state.client} />
                    ) : this.state.selectedTab == 2 ? (
                        <ContactsAndBranchesSection client={this.state.client} />
                    ) : this.state.selectedTab == 3 ? (
                        <MemberFieldsSection client={this.state.client} />
                    ) : this.state.selectedTab == 4 ? (
                        <ServicingInstructionsSection client={this.state.client} />
                    ) : this.state.selectedTab == 5 ? (
                        <HMOInformationSection client={this.state.client} history={this.props.history} />
                    ) : this.state.selectedTab == 6 ? (
                        <MembersSection client={this.state.client} />
                    ) : this.state.selectedTab == 7 ? (
                        <HistorySection client={this.state.client} />
                    ) : this.state.selectedTab == 8 ? (
                        <DocumentsSection client={this.state.client} />
                    ) : this.state.selectedTab == 9 ? (
                        <AuditLogsSection client={this.state.client} />
                    ) : null
                ) : (
                    <Loading height='10rem'/>
                )}
                
            </Grid>
        );
    }
}