import * as React from 'react';
import { Grid, TableRow, TableCell } from '@material-ui/core';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { faSort, faCheck, faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { API } from 'Pages/CustomerCarePage/API';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';

interface IProps {
    clientId: string;
}

interface IStates {
    code: string;
    name: string;
    location: string;

    type?: string;
    groups: PartnerNetworkGroup[];
    facilities: FacilityData[];

    isLoaded: boolean;
}

interface PartnerNetworkGroup {
    checked: boolean;
    name: string;
    label: string;
}

interface FacilityData {
    code: string;
    name: string;
    location: string;
}

export class PartnerNetworkSection extends React.Component<IProps, IStates> {

    groupChoices: PartnerNetworkGroup[] = [
        { checked: false, name: 'Top 5 Hospitals', label: 'top_5_hospitals' },
        { checked: false, name: 'Top 6 Hospitals', label: 'top_6_hospitals' },
        { checked: false, name: 'Healthway Clinics', label: 'healthway_clinics' },
        { checked: false, name: 'Medical City Network', label: 'medical_city_network' }
    ]

    constructor(props: IProps) {
        super(props);

        this.state = {
            code: '',
            name: '',
            location: '',

            groups: [],
            facilities: [],
            isLoaded: false
        }
    }

    componentDidMount() {
        API.get(`clients/account/hmo-info/${this.props.clientId}`,
            (response) => {
                constructPartnerNetworkData(response.benefit_plan_tree);
                this.setState({isLoaded: true});
            },
            (error) => {
                console.log(error);
            });

        const constructPartnerNetworkData = (response: any[]) => {

            const lastElement = (array: any[]) => {
                return array[array.length - 1];
            }

            const parentNode = response.find(node => node.level == 4 && lastElement(node.tree_path) === 'NT');

            const childrenNodes = response.filter(node => {
                return parentNode.children.includes(node._id);
            });

            const nodesWithType = childrenNodes.filter(node => {
                return (
                    lastElement(node.tree_path) === 'CB' ||
                    lastElement(node.tree_path) === 'OD'
                );
            });

            const nodeWithPNList = nodesWithType.find(node => {
                return node.custom_metadata.partner_network_list;
            })

            console.log('childrenNodes', childrenNodes);
            console.log('nodesWithType', nodesWithType);
            console.log('nodeWithPNList', nodeWithPNList);

            const pnGroups = parentNode.custom_metadata.groups;
            let pnType = undefined;
            let pnFacilities = undefined;

            if (nodeWithPNList) {
                pnType = nodeWithPNList.name;
                pnFacilities = nodeWithPNList.custom_metadata.partner_network_list;
            }
            
            console.log('pnType', pnType);
            console.log('pnGroups', pnGroups);
            console.log('pnFacilities', pnFacilities);
            
            /* Assign Type */
            // @ts-ignore
            this.setState({type: pnType});

            /* Assign Groups */
            if (pnGroups) {
                this.setState({groups: this.groupChoices.map(i => i)});
                this.state.groups.forEach(group => {
                    group.checked = pnGroups.find(pnGroup => group.label === pnGroup)
                })
                this.setState({groups: this.state.groups});
            }

            /* Assign Facilities */
            if (pnFacilities) {
                // @ts-ignore
                this.setState({facilities: pnFacilities.map(facility => {
                    return {
                        code: facility.facility_id,
                        name: facility.name,
                        location: '---'
                    }
                })});
            }
        }
    }

    getFilteredFacilitiesData = () => {
        const includes = (from: string, to: string) => {
            return from.toLowerCase().includes(to.toLowerCase());
        };
        const { code, name, location } = this.state;

        const filteredFacilities = this.state.facilities
            .filter(facility => code === '' || includes(facility.code, code))
            .filter(facility => name === '' || includes(facility.name, name))
            .filter(facility => location === '' || includes(facility.location, location));

        return filteredFacilities;
    }

    sortFacilities(attrName: string, isAsc: boolean): void {
        this.state.facilities.sort((a: any, b: any) => 
            (a[attrName].toString().toLowerCase() > b[attrName].toString().toLowerCase()) ? (isAsc ? 1 : -1) : (isAsc ? -1 : 1)
        );

        this.setState({facilities: this.state.facilities});
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                {!this.state.isLoaded ? (
                    <Loading height='10rem'/>
                ) : (
                    this.renderMainContent()
                )}
            </div>
        );
    }

    renderMainContent(): JSX.Element {
        return (
            <>
            <div style={{paddingTop: '2.5rem'}} />
            <div className='header-1'>Check Partner Network</div>
            <div className='sub-section'>
                <div className='header-2'>TYPE</div>
                <div className='small-text'>{this.state.type ? this.state.type : '---'}</div>
            </div>
            <div className='sub-section'>
                <div className='header-2'>GROUP</div>
                <Grid className='sub-section' container spacing={2} style={{width: '50%'}}>
                    {this.groupChoices.map(group => {
                        return (
                            <Grid item xs={6}>
                                <span>
                                    {group.checked ? (
                                        <FontAwesomeIcon icon={faCheck} style={{color: '#3AB77D'}} />
                                    ) : (
                                        <FontAwesomeIcon icon={faTimes} style={{color: '#FD5451'}} />
                                    )}
                                </span>
                                <span style={{paddingLeft: '1rem'}}>{group.name}</span>
                            </Grid>
                        );
                    })}
                </Grid>
            </div>
            <div className='sub-section'>
                <Grid className='sub-section' container spacing={2}>
                    <Grid item xs={2}>
                        <TextButton label='Excluded Hospitals' onClick={() => {}} />
                    </Grid>
                    <Grid item xs={2}>
                        <TextButton label='Included Hospitals' onClick={() => {}} />   
                    </Grid>
                </Grid>
                <CustomTable
                    rows={this.getFilteredFacilitiesData()}
                    renderHeader={() => this.renderTableHeader()}
                    renderRow={(rowData: any, index: number) => 
                        this.renderTableRow(rowData, index)} />
            </div>
            <div className='section' />
            </>
        );
    }

    renderTableHeader(): JSX.Element {
        
        return (
            <>
            {this.renderHeaderColumn('code', 'Facility Code')}
            {this.renderHeaderColumn('name', 'Name')}
            {this.renderHeaderColumn('location', 'Location')}
            </>
        );
    }

    renderTableRow(facility: any, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{facility.code}</TableCell>
                <TableCell>{facility.name}</TableCell>
                <TableCell>{facility.location}</TableCell>
            </TableRow>
        );
    }

    renderHeaderColumn(attrName: string, headerText: string): JSX.Element {
        return (
            <TableCell>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            this.sortFacilities(attrName, true);
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        // @ts-ignore
                        this.setState({[attrName]: value});
                    }} />
            </TableCell>
        );
    }
}