import * as React from 'react';
import { Grid, TableRow, TableCell } from '@material-ui/core';
import { ClientData, PlanTypeData } from 'Pages/CustomerCarePage/Models';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { faSort } from '@fortawesome/free-solid-svg-icons';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';

interface IProps {
    history: any;
    client: ClientData;
}

interface IStates {
    filterName: string;
    filterBenefits: string;
    filterPrincipals: string;
    filterDependents: string;
    filterDateCreated: string;
    filterDateUpdated: string;
    filterVersion: string;
}

export class HMOInformationSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            filterName: '',
            filterBenefits: '',
            filterPrincipals: '',
            filterDependents: '',
            filterDateCreated: '',
            filterDateUpdated: '',
            filterVersion: ''
        };
    }

    render() : JSX.Element {
        const client = this.props.client;
        const contract = client.contract;
        const planTypes = client.planTypes;

        const startDate = contract && contract.startDate ? GlobalFunction.toShortDateString(contract.startDate) : '---';
        const endDate = contract && contract.endDate ? GlobalFunction.toShortDateString(contract.endDate) : '---';
        const brokerName = client.salesChannel ? client.salesChannel.contactName : '---';
        const brokerCompany = client.salesChannel ? client.salesChannel.company : '---';

        return (
            <Grid container spacing={2}>
                {this.renderItem('CORPORATE ACCOUNT NO.', client.corporateAccountNo)}
                {contract ? (
                    <>
                    {this.renderItem('CONTRACT', contract.name, 9, 
                        () => {
                            this.props.history.push(`#/membership/member-contract/${contract.id}`);
                            window.location.reload();
                        })}
                    {this.renderItem('EFFECTIVITY DATE', startDate)}
                    {this.renderItem('EXPIRY DATE', endDate)}
                    {this.renderItem('REGISTERED COMPANY NAME', client.registeredName, 6)}
                    {this.renderItem('AUTHORIZED SIGNATORY', client.authSignatory || '---')}
                    {this.renderItem('BROKER/BDO', brokerName)}
                    {this.renderItem('BROKER COMPANY', brokerCompany, 6)}
                    </>
                ) : null}
                <Grid item xs={12} style={{overflowX: 'auto'}}>
                    {planTypes ? (
                        <CustomTable
                            rows={planTypes}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderTableRow(rowData, index)} />
                    ) : null}
                </Grid>
            </Grid>
        );
    }

    renderItem(label: string, value: string, xs?: any, onClick?: () => void): JSX.Element {
        return (
            <Grid item className='sub-section' xs={xs ? xs : 3} style={{padding: '16px 8px'}}>
                <div className='light-text'>{label}</div>
                {onClick ? (
                    <TextButton label={value} onClick={onClick} />
                ) : (
                    <div className='header-2'>{value}</div>
                )}
                
            </Grid>
        )
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            {this.renderHeaderColumn('fiterName', 'Plan Type')}
            {this.renderHeaderColumn('filterBenefits', 'Benefits')}
            {this.renderHeaderColumn('filterPrincipals', 'Principals')}
            {this.renderHeaderColumn('filterDependents', 'Dependents')}
            {this.renderHeaderColumn('filterDateCreated', 'Date Created')}
            {this.renderHeaderColumn('filterDateUpdated', 'Date Modified')}
            {this.renderHeaderColumn('filterVersion', 'Contract Version')}
            </>
        );
    }

    renderTableRow(planType: PlanTypeData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{planType.name}</TableCell>
                <TableCell>{planType.benefits}</TableCell>
                <TableCell>{planType.principals}</TableCell>
                <TableCell>{planType.dependents}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(planType.dateCreated)}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(planType.dateUpdated)}</TableCell>
                <TableCell>{planType.version}</TableCell>
            </TableRow>
        );
    }

    renderHeaderColumn(attrName: string, headerText: string, width?: string): JSX.Element {
        return (
            <TableCell style={{minWidth: width ? width : '150px'}}>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            // this.sortFacilities(attrName, true);
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        // @ts-ignore
                        this.setState({[attrName]: value});
                    }} />
            </TableCell>
        );
    }
}