interface IProps {
    required: string;
    match: match<MatchParams>;
    history: any;
}

interface MatchParams {
    id: string;
}

interface IStates {
    loadedResources: number;
    isAssessmentModalOpen: boolean;
    isAssignModalOpen: boolean;
    isEscalateModalOpen: boolean;
    isOtherActionsPopperOpen: boolean;
    isSaving: boolean;
    snackbarMessage: string | null;
    lastSave?: Date;
    popperAnchor: HTMLButtonElement | null;
    permission: PermissionData;
    assigneeNotes: {
        isModalOpen: boolean;
        previousAssignee: string;
        notes: string;
    };
    currentCase?: CaseData;

    /* Can save to tmp */
    tabSelected: number;
    member?: MemberData;
    client?: ClientData;
    ticket?: TicketData;
    confinement?: ConfinementDetails;
    inquiries?: InquiryData[];
    comments: CommentData[];

    updateState: UpdateData;
    accommodationState: AccommodationData;
    diagnosisState: DiagnosisData;
    physicianState: PhysicianData;
    procedureState: ProcedureData;
    otherDetailState: ItemData;
    fileState: FileData;
}

export interface ConfinementDetails {
    provider: DropdownChoice;
    dateAdmitted: Date;
    confinementKind: string;
    giftGiven: boolean;
    coverage: DropdownChoice;
    ticketNo: string;
    paidBy: string;
    philhealthAmount: number;
    finalBill: number;
    dischargeDate?: Date;
    primaryDiagnosisIndex: number;

    updates: UpdateData[];
    accommodations: AccommodationData[];
    diagnoses: DiagnosisData[];
    physicians: PhysicianData[];
    procedures: ProcedureData[];
    items: ItemData[];
    files: FileData[];
}

interface ErrorMessageFormat {
    message: string;
    targets: string[];
}

export class UpdateTicketPage extends React.Component<IProps, IStates> {

    static RESOURCES_TO_LOAD = 5;

    inquiryTabList = [
        { value: 1, label: 'Inquiry Details' }, 
        { value: 2, label: 'Comments' }, 
        { value: 3, label: 'Benefits' }, 
        { value: 4, label: 'Partner Network' }, 
        // { value: 5, label: 'Exclusion' }, 
        { value: 6, label: 'Utilization' }, 
        { value: 7, label: 'Tickets' },  
        { value: 8, label: 'Contract' }
    ];

    availmentTabList = [
        { value: 1, label: 'Summary' }, 
        { value: 2, label: 'Updates' }, 
        { value: 3, label: 'Room Accommodations' }, 
        { value: 4, label: 'Diagnoses' }, 
        { value: 5, label: 'Physicians' }, 
        { value: 6, label: 'Procedures' },  
        { value: 7, label: 'PhilHealth & Discharge Details' }, 
        { value: 8, label: 'Other Details' }, 
        { value: 9, label: 'Uploaded Documents' }
    ];

    icd10Codes: DropdownChoice[];
    cptCodes: DropdownChoice[];
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    agents: DropdownChoice[];

    errors: ErrorMessageFormat[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            tabSelected: 1,
            loadedResources: 0,
            isAssessmentModalOpen: false,
            isAssignModalOpen: false,
            isEscalateModalOpen: false,
            isOtherActionsPopperOpen: false,
            isSaving: false,
            snackbarMessage: null,
            popperAnchor: null,
            permission: {},
            assigneeNotes: {
                isModalOpen: false,
                previousAssignee: '',
                notes: ''
            },
            updateState: {
                remarks: '',
                date: new Date(),
                hospitalBill: 0
            },
            accommodationState: {
                roomType: '',
                days: 0,
                costPerDay: 0
            },
            diagnosisState: {
                diagnosis: { value: '', label: '' },
                remarks: '',
                isFinal: false,
                isPrimary: false
            },
            physicianState: {
                physician: { value: '', label: '', params: ['', 'Admitting'] },
                diagnosis: { value: '', label: '' },
                remarks: '',
                visits: 0,
                amount: 0
            },
            procedureState: {
                procedure: { value: '', label: '' },
                diagnosis: { value: '', label: '' },
                physician: { value: '', label: '' },
                remarks: '',
                date: new Date(),
                amount: 0
            },
            otherDetailState: {
                name: '',
                type: { value: '', label: '' },
                amount: 0
            },
            fileState: {
                docName: '',
                file: null
            },
            comments: []
        };

        this.facilities = [];
        this.agents = [];
        this.doctors = [];
        this.cptCodes = [];
        this.icd10Codes = [];
        this.errors = [];
    }










    componentDidMount() {

        API.get(`tmp?` +
            `filter[where][and][0][user_id]=${localStorage['user_id']}&` +
            `filter[where][and][1][module]=Customer%20Care&` + 
            `filter[where][and][2][ticket_no]=${this.props.match.params['ticketId']}`,
            (response) => {
                if (response.length > 0) {
                    this.setState({
                        lastSave: new Date(response[0].timestamp),
                        loadedResources: this.state.loadedResources + 1,
                        tabSelected: response[0].data.tab_selected,
                        ticket: response[0].data.ticket,
                        confinement: response[0].data.confinement,
                        inquiries: response[0].data.inquiries,
                        updateState: response[0].data.update_state,
                        accommodationState: response[0].data.accommodation_state,
                        diagnosisState: response[0].data.diagnosis_state,
                        physicianState: response[0].data.physician_state,
                        procedureState: response[0].data.procedure_state,
                        otherDetailState: response[0].data.other_detail_state,
                        fileState: response[0].data.file_state,
                        permission: response[0].data.permission
                    });
                    if (response[0].data.member_id) {
                        API.get(`customer-care/members/${response[0].data.member_id}`, 
                            (response) => {
                                this.setState({member: Payloads.getMemberData(response)});
                                API.get(`customer-care/members/${response.member_id}/benefits`, () => {});
                            });
                    }
    
                    if (response[0].data.client_id) {
                        API.get(`customer-care/clients/${response[0].data.client_id}`,
                            (response) => {
                                this.setState({client: Payloads.getClientData(response)});
                            });
                    }
                } else {
                    this.loadTicketData(`customer-care/tickets/${this.props.match.params['ticketId']}`);
                }
            },
            () => {
                this.loadTicketData(`customer-care/tickets/${this.props.match.params['ticketId']}`);
            });

        API.get('facilities',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.facilities = response.map(facility => {
                    return { 
                        value: facility.facility_id,
                        label: facility.name 
                    }
                });
            },
            (error) => {
                console.log(error);
            });

        API.get('icd10-codes?filter[limit]=1000',
            (response) => {
                console.log(response);  
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.icd10Codes = response.map(icd10Code => {
                    return { 
                        value: icd10Code.full_code,
                        label: icd10Code.abbreviated_description 
                    };
                });
            },
            (error) => {
                console.log(error);
            });

        API.get('cpts?filter[limit]=1000',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.cptCodes = response.map(cptCode => {
                    return {
                        value: cptCode.code,
                        label: cptCode.label 
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });

        API.get('doctors',
            (response) => {
                this.setState({loadedResources: this.state.loadedResources + 1});
                this.doctors = response.map(doctor => {
                    return {
                        value: doctor._id,
                        label: ('Dr. ' + doctor.first_name +  ' ' + doctor.last_name),
                        params: [doctor.type]
                    };
                });
            }, 
            (error) => {
                console.log(error);
            });
    }









    
    loadTicketData = (url: string) => {
        API.get(url, 
            (response) => {
                console.log(response);
                this.setState({loadedResources: this.state.loadedResources + 1});
                const ticketCategory = response.ticket_category;
                const confinement = ticketCategory.confinement_details;
                const inquiry = ticketCategory.inquiry_details;

                this.setState({ticket: Payloads.getTicketData(response)});
  
                Processmaker.get('cases',
                    (cases) => {
                        console.log(cases);

                        if (this.state.ticket) {
                            const ticketNo = this.state.ticket.ticketNo;
                            const caseId = ticketNo.substring(ticketNo.length - 4, ticketNo.length);
                            const targetCase = cases.find(caze => caze.app_number === caseId);
                            
                            if (targetCase) {
                                this.setState({currentCase: {
                                    task: targetCase.app_tas_title
                                }});
                                if (targetCase.app_tas_title === 'Inquiry - Comment' ||
                                    targetCase.app_tas_title === 'Inquiry - Escalate Ticket') {
                                    this.setState({permission: {
                                        canAssign: true,
                                        canEscalate: true,
                                        canComment: true
                                    }});
                                } else if (
                                    targetCase.app_tas_title === 'Inquiry - View Details' ||
                                    targetCase.app_tas_title === 'Inquiry - View Ticket Details 2') {
                                    this.setState({permission: {
                                        canUpdate: true,
                                        canUploadFile: true,
                                        canAssign: true,
                                        canEscalate: true,
                                        canCancel: false, // For now, just to disable the cancel button
                                        canResolve: true,
                                        canClose: true,
                                        canComment: true
                                    }});
                                }

                                if (targetCase.app_title.length > 5) {
                                    const appTitle = JSON.parse(targetCase.app_title);
                                    this.state.assigneeNotes.isModalOpen = true;
                                    this.setState({assigneeNotes: {
                                        isModalOpen: appTitle.assigned_by.length > 0 || appTitle.assign_remarks > 0,
                                        previousAssignee: appTitle.assigned_by,
                                        notes: appTitle.assign_remarks
                                    }});
                                }
                            } else {

                            }
                        }

                        if (!this.state.permission.canComment) {
                            this.inquiryTabList.splice(1, 1);
                        }
                    },
                    (error) => {
                        console.log(error);
                    }
                );

                if (confinement) {
                    this.setState({confinement: Payloads.getConfinementData(confinement, response)});
                }
                
                if (inquiry) {
                    this.setState({inquiries: inquiry.inquiry_list.map(value => {
                        return {
                            inquiry: { value: '', label: value.inquiry },
                            detail: value.value,
                            specificDetail: value.specific_detail,
                            answer: value.answer,
                            remarks: value.remarks,
                            relatedTickets: value.related_tickets,
                            supportDocs: value.support_docs
                        };
                    })});

                    this.setState({comments: response.comments.map(comment => {
                        return {
                            sender: comment.user_name,
                            message: comment.comment,
                            date: new Date(comment.date_created)
                        }
                    })});
                }

                if (response.member_id.trim().length > 0) {
                    API.get(`customer-care/members/${response.member_id}`, 
                        (response) => {
                            this.setState({member: Payloads.getMemberData(response)});
                            API.get(`customer-care/members/${response.member_id}/benefits`, () => {});
                        });
                }

                if (response.client_id.trim().length > 0) {
                    API.get(`customer-care/clients/${response.client_id}`,
                        (response) => {
                            this.setState({client: Payloads.getClientData(response)});
                        });
                }
                
            },
            (error) => {
                console.log(error);
            });
    }










    getErrorList = () => {
        let errors: ErrorMessageFormat[] = [];

        const confinement = this.state.confinement;
        
        if (confinement) {

            /* Check if there's a physician assigned to each diagnosis */
            let errorDiagnosis = confinement.diagnoses.filter(diagnosis => {
                let matched = false;
                confinement.physicians.map(physician => {
                    matched = physician.diagnosis.value === diagnosis.diagnosis.value ? true : matched;
                });
                return !matched;
            });

            if (errorDiagnosis.length > 0) {
                errors.push({
                    message: 'Please assign a physician for the following diagnoses before proceding:',
                    targets: errorDiagnosis.map(diagnosis => {
                        return `${diagnosis.diagnosis.value} ${diagnosis.diagnosis.label}`
                    })
                });
                this.setState({tabSelected: 5});
            }
        }
        return errors;
    }










    onUploadFiles = (files: FileData[], key: string, nextAction: Function) => {
        let hasFiles = false;

        const formData = new FormData();
        files.forEach(file => {
            if (file.file && file.file['path'] && !file.file['field']) {
                formData.append(key, file.file);
                hasFiles = true;
            }
        });

        this.setState({isSaving: true});

        if (hasFiles) {
            API.postFormData('tmp/upload?path=customer-care', formData,
                (uploadResponse) => {
                    nextAction(uploadResponse);
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to upload files.'
                    });
                });
        } else {
            nextAction([]);
        }
    }










    onUploadInquiryFiles = (files: File[], key: string, nextAction: Function) => {
        let hasFiles = false;

        const formData = new FormData();
        files.forEach(file => {
            if (!file['field']) {
                formData.append(key, file);
                hasFiles = true;
            }
        });

        this.setState({isSaving: true});

        if (hasFiles) {
            API.postFormData('tmp/upload?path=customer-care', formData,
                (uploadResponse) => {
                    nextAction(uploadResponse);
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to upload files.'
                    });
                });
        } else {
            nextAction([]);
        }
    }










    onUpdateTicket = (markAsResolve: boolean, onSuccess: Function) => {
        const ticket = this.state.ticket;
        const confinement = this.state.confinement;
        const inquiries = this.state.inquiries;

        if ((!this.state.member && !this.state.client) || (!confinement && !inquiries) || !ticket) return;

        const fileData = this.state.confinement ? (
            this.state.confinement.files.filter(file => file.file ? !file.file['field'] : false) 
        ) : [];

        const putTicket = (supportingFiles: any, inquiryFiles: File[][]) => {
            const payload = Payloads.getPutTicketPayload(
                ticket,
                !supportingFiles.error && supportingFiles.length > 0 ? (
                    confinement ? (
                        confinement.files
                            .map(file => { 
                                return {
                                    ...file.file,
                                    docName: file.docName
                                }
                            })
                            .filter(file => file ? file['field'] : false)
                            .concat(
                                supportingFiles.map((response, index) => {
                                    return {
                                        ...response,
                                        docName: fileData[index].docName
                                    }
                                })
                            )
                    ) : [] 
                ) : (
                    confinement ? (
                        confinement.files.map(file => {
                            return {
                                ...file.file,
                                docName: file.docName
                            }
                        })
                    ) : []
                ),
                inquiryFiles,
                this.state.member, 
                this.state.client,
                this.state.confinement,
                this.state.inquiries,
                markAsResolve
            );

            console.log(payload);
            API.put(`customer-care/tickets/${this.props.match.params['ticketId']}`, payload,
                (response) => {
                    console.log(response);
                    if (markAsResolve) {
                        onResolveProcessmaker();
                    } else {
                        onSuccess();
                    }
                    
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to update ticket.'
                    });
                });
        }

        this.onUploadFiles(fileData, 'supporting_document', (supportingFiles) => {
            
            const inquiryFiles: File[][] = [];

            if (this.state.inquiries && this.state.inquiries.length > 0) {
                this.state.inquiries.forEach(inquiry => {
                    this.onUploadInquiryFiles(inquiry.supportDocs, 'supporting_document',
                        (inquiryFilesResponse) => {
                            inquiryFiles.push(
                                !inquiryFilesResponse.error && 
                                inquiryFilesResponse.length > 0 ? (
                                    inquiry.supportDocs
                                        .filter(file => file['field'])
                                        .concat(inquiryFilesResponse)
                                ) : (
                                    inquiry.supportDocs
                                )
                            );

                            if (this.state.inquiries && 
                                this.state.inquiries.length === inquiryFiles.length) {
                                putTicket(supportingFiles, inquiryFiles);
                            }
                        }
                    );
                });

            } else {
                putTicket(supportingFiles, inquiryFiles);
            }
        });

        const pmCallShortcut = (method: 'POST' | 'PUT', url: string, body: any, onSuccess: Function) => {
            Processmaker.shortcutCall(method, url, body, onSuccess, () => {
                this.setState({
                    isSaving: false,
                    snackbarMessage: 'Failed to create ticket.'
                });
            })
        }

        const onResolveProcessmaker = () => {
            const caseId = this.props.match.params['caseId'];
            const body = {
                button_pressed: 'RESOLVE'
            };

            pmCallShortcut('PUT', `cases/${caseId}/variable`, body, () => {
                pmCallShortcut('PUT', `cases/${caseId}/route-case`, {}, () => {
                    onSuccess();
                });
            });
        }
    }










    saveForNow = () => {
        const ticket = this.state.ticket;
        const client = this.state.client;
        const member = this.state.member;
        const confinement = this.state.confinement;
        const inquiries = this.state.inquiries;

        if ((!member && !client) || (!confinement && !inquiries) || !ticket) return;

        const fileData = confinement ? confinement.files : [];

        const putTicket = (supportingFiles: any, inquiryFiles: File[][]) => {
            const payload = {
                user_id: localStorage['user_id'],
                ticket_no: ticket.id,
                module: 'Customer Care',
                process: 'Update Ticket',
                timestamp: new Date(),
                data: {
                    tab_selected: this.state.tabSelected,
                    member_id: this.state.member ? this.state.member.memberId : undefined,
                    client_id: this.state.client ? this.state.client.corporateAccountNo : undefined,
                    ticket: this.state.ticket,
                    confinement: confinement ? {
                        ...confinement,
                        files: confinement.files
                        .map(file => { 
                            return {
                                ...file.file,
                                docName: file.docName
                            }
                        })
                        .filter(file => file ? file['field'] : false)
                        .concat(
                            supportingFiles.map((response, index) => {
                                return {
                                    ...response,
                                    docName: fileData[index].docName
                                }
                            })
                        )
                    } : undefined,
                    inquiries: this.state.inquiries ? (
                        this.state.inquiries.map((inquiry, index) => {
                            return {
                                ...inquiry,
                                supportDocs: inquiryFiles[index]
                            }
                        })
                    ) : undefined,
                    update_state: this.state.updateState,
                    accommodation_state: this.state.accommodationState,
                    diagnosis_state: this.state.diagnosisState,
                    physician_state: this.state.physicianState,
                    procedure_state: this.state.procedureState,
                    other_detail_state: this.state.otherDetailState,
                    file_state: this.state.fileState,
                    permission: this.state.permission
                }
            };
            
            API.post('tmp', JSON.stringify(payload),
                (response) => {
                    console.log(response);
                    if (response._id) {
                        this.setState({
                            isSaving: false,
                            snackbarMessage: 'Progress saved.',
                            lastSave: new Date()
                        });
                    } else {
                        this.setState({
                            isSaving: false,
                            snackbarMessage: 'Failed to save progress.'
                        });
                    }
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to save progress.'
                    });
                });
        }

        this.onUploadFiles(fileData, 'supporting_document', (supportingFiles) => {
            
            const inquiryFiles: File[][] = [];

            if (this.state.inquiries && this.state.inquiries.length > 0) {
                this.state.inquiries.forEach(inquiry => {
                    this.onUploadInquiryFiles(inquiry.supportDocs, 'supporting_document',
                        (inquiryFilesResponse) => {
                            inquiryFiles.push(
                                !inquiryFilesResponse.error && 
                                inquiryFilesResponse.length > 0 ? (
                                    inquiry.supportDocs
                                        .filter(file => file['field'])
                                        .concat(inquiryFilesResponse)
                                ) : (
                                    inquiry.supportDocs
                                )
                            );

                            if (this.state.inquiries && 
                                this.state.inquiries.length === inquiryFiles.length) {
                                putTicket(supportingFiles, inquiryFiles);
                            }
                        }
                    );
                });

            } else {
                putTicket(supportingFiles, inquiryFiles);
            }
        });
    }










    render(): JSX.Element {
        return (
            <Grid container className='customer-care-main'>
                {this.state.loadedResources !== UpdateTicketPage.RESOURCES_TO_LOAD || this.state.isSaving ? (
                    <div 
                        style={{
                            backgroundColor: '#00000055', 
                            zIndex: 100, 
                            position: 'fixed', 
                            width: '100%', 
                            height: '100%'}}>
                        <Loading height='15rem' size={100}/>
                    </div>
                    
                ) : (null)}

                <Snackbar
			        anchorOrigin={{
			          vertical: 'top',
			          horizontal: 'right',
			        }}
			        open={this.state.snackbarMessage ? true : false}
			        autoHideDuration={6000}
			        onClose={() => {
                        this.setState({snackbarMessage: null});
                    }}
                    message={this.state.snackbarMessage} />

                <CustomModal
                    className='modal'
                    isOpen={this.state.isAssessmentModalOpen}
                    handleClose={() => this.setState({isAssessmentModalOpen: false})}
                    header='Missing Information'
                    content={
                        <div className='header-2'>
                            {this.errors.map(error => {
                                return (
                                    <>
                                    <p>{error.message}</p>
                                    <p>
                                        {error.targets.map(target => {
                                            return <div>{`- ${target}`}</div>
                                        })}
                                    </p>
                                    </>
                                );
                            })}
                        </div>
                    }
                    footer={
                        <CustomButton 
                            text='Okay'
                            variant='outlined'
                            onClick={() => {
                                this.setState({isAssessmentModalOpen: false});
                            }} />
                    } />

                {this.state.ticket && this.state.currentCase ? (
                    <ReassignTicketModal 
                        caseId={this.props.match.params['caseId']}
                        ticket={this.state.ticket}
                        currentCase={this.state.currentCase}
                        isAssignModalOpen={this.state.isAssignModalOpen} 
                        onSuccess={() => {
                            this.setState({isAssignModalOpen: false});
                            this.props.history.push('#/customer-care/');
                            window.location.reload();
                        }}
                        onClose={() => {
                            this.setState({isAssignModalOpen: false});
                        }} />
                ) : null}
                
                {this.state.ticket ? (
                    <EscalateTicketModal
                        caseId={this.props.match.params['caseId']}
                        ticket={this.state.ticket}
                        isEscalateModalOpen={this.state.isEscalateModalOpen}
                        onSuccess={() => {
                            this.setState({isEscalateModalOpen: false});
                            this.props.history.push('#/customer-care/');
                            window.location.reload();
                        }}
                        onClose={() => {
                            this.setState({isEscalateModalOpen: false});
                        }} />
                ) : null}

                <CustomModal
                    className='modal'
                    isOpen={this.state.assigneeNotes.isModalOpen}
                    handleClose={() => {
                        this.state.assigneeNotes.isModalOpen = false;
                        this.setState({assigneeNotes: this.state.assigneeNotes});
                    }}
                    header='Assignee Notes'
                    content={
                        <>
                        <div className='sub-section-2'>
                            <div className='light-text'>PREVIOUS ASSIGNEE</div>
                            <div className='text'>{this.state.assigneeNotes.previousAssignee}</div>
                        </div>
                        <div className='sub-section-2'>
                            <div className='light-text'>NOTES</div>
                            <div className='text'>
                                {this.state.assigneeNotes.notes ? this.state.assigneeNotes.notes : '---'}
                            </div>
                        </div>
                        </>
                    } />

                <ItemListPopper 
                    isOpen={this.state.isOtherActionsPopperOpen}
                    anchor={this.state.popperAnchor}
                    items={[
                        {
                            disabled: !this.state.permission.canEscalate,
                            content: (
                                <TextButton 
                                    icon={faArrowUp} 
                                    label='Escalate' 
                                    underline='none' 
                                    color='initial'
                                    onClick={() => {}} />
                            ),
                            onClick: () => {
                                this.setState({
                                    isOtherActionsPopperOpen: false,
                                    isEscalateModalOpen: true
                                });
                            },

                        },
                        {
                            disabled: !this.state.permission.canCancel,
                            content: (
                                <TextButton 
                                    icon={faTimes} 
                                    label='Cancel Ticket' 
                                    underline='none'
                                    color='initial'
                                    onClick={() => {}} />
                            ),
                            onClick: () => {
                                this.setState({
                                    isOtherActionsPopperOpen: false,
                                    // isAssignModalOpen: true
                                });
                            }
                        }
                    ]} />
                
                {this.state.ticket ? (
                    <LeftPanelSection 
                        member={this.state.member}
                        client={this.state.client}
                        ticket={this.state.ticket}
                        task={this.state.permission.canUpdate ? 'Update' : 'View'}
                        disableLinkedTickets
                        onReassign={this.state.permission.canAssign ? () => {
                            this.setState({isAssignModalOpen: true});
                        } : undefined} />
                ) : null}

                {this.state.confinement ? (
                    this.renderInpatientContent(this.state.confinement)
                ) : this.state.inquiries ? (
                    this.renderInquiryContent(this.state.inquiries)
                ) : null}

                {this.state.ticket ? (
                    <TicketFooter
                        disableFinalAssessment={this.state.ticket.ticketType === 'Inquiry'}
                        lastSave={this.state.lastSave}
                        onSaveForNow={this.state.permission.canUpdate ? () => {
                            this.saveForNow();
                        } : undefined} 
                        onResolveAndCloseTicket={this.state.permission.canResolve ? () => {
                            this.onUpdateTicket(true, () => {
                                this.props.history.push(
                                    '#/customer-care/resolve-ticket/' +
                                    this.props.match.params['caseId'] + '/' +
                                    this.props.match.params['ticketId']);
                                window.location.reload();
                            });
                        } : undefined}
                        onFinalAssessment={!this.state.inquiries ? () => {
                            this.errors = this.getErrorList();
                            if (this.errors.length == 0) {
                                this.onUpdateTicket(false, () => {
                                    this.props.history.push(
                                        '#/customer-care/resolve-ticket/' +
                                        this.props.match.params['caseId'] + '/' +
                                        this.props.match.params['ticketId']);
                                    window.location.reload();
                                });
                            } else {
                                this.setState({isAssessmentModalOpen: true});
                            }
                        } : undefined}
                        onUpdateTicket={this.state.permission.canUpdate ? () => {
                            this.onUpdateTicket(false, () => {
                                this.props.history.push('#/customer-care/tickets');
                                window.location.reload();
                            });
                        } : undefined}
                        leftButtons={this.state.inquiries ? [
                            <CustomButton 
                                className='button black-text'
                                color='default'
                                text='Other Actions'
                                onClick={(event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
                                    this.setState({
                                        popperAnchor: event.currentTarget,
                                        isOtherActionsPopperOpen: !this.state.isOtherActionsPopperOpen
                                    });
                                }} />
                        ] : undefined} />
                ) : null}
            </Grid>
        );
    }










    renderInpatientContent(confinement: ConfinementDetails): JSX.Element {
        return (
            <div className='main-content'>
                <CustomTabs 
                    tabList={this.availmentTabList}
                    selectedTab={this.state.tabSelected}
                    setSelectedTab={(index: number) => {
                        this.setState({tabSelected: index});
                    }} />
                    
                {this.state.tabSelected === 1 && this.state.ticket ? (
                    <SummarySection 
                        ticketNo={this.state.ticket.ticketNo} 
                        updates={confinement.updates}
                        accommodations={confinement.accommodations}
                        diagnoses={confinement.diagnoses}
                        physicians={confinement.physicians}
                        procedures={confinement.procedures}
                        items={confinement.items}
                        provider={confinement.provider}
                        dateAdmitted={confinement.dateAdmitted}
                        confinementKind={confinement.confinementKind}
                        coverage={confinement.coverage}
                        philhealthAmount={confinement.philhealthAmount}
                        finalBill={confinement.finalBill}
                        dischargeDate={confinement.dischargeDate}
                        setTabSelected={(value: number) => this.setState({tabSelected: value})}/>
                    
                ) : this.state.tabSelected === 2 ? (
                    <UpdateSection 
                        confinement={confinement}
                        updates={confinement.updates}
                        state={this.state.updateState}
                        refreshUpdatesState={() => {
                            this.setState({confinement: confinement});
                        }} /> 

                ) : this.state.tabSelected === 3 ? (
                    this.state.ticket ? (
                        <AccommodationsSection
                            accommodations={confinement.accommodations}
                            state={this.state.accommodationState}
                            facilitiesList={this.facilities}
                            provider={confinement.provider}
                            dateAdmitted={confinement.dateAdmitted}
                            confinementKind={confinement.confinementKind}
                            refreshAccommodationsState={() => {
                                this.setState({confinement: confinement});
                            }} />
                    ) : null

                ) : this.state.tabSelected === 4 ? (
                    this.state.ticket ? (
                        <DiagnosesSection 
                            diagnoses={confinement.diagnoses}
                            state={this.state.diagnosisState}
                            physicians={confinement.physicians}
                            procedures={confinement.procedures}
                            diagnosesList={this.icd10Codes}
                            primaryDiagnosisIndex={confinement.primaryDiagnosisIndex}
                            refreshDiagnosisStateChange={() => {
                                this.setState({confinement: confinement});
                            }}
                            setPrimaryDiagnosisIndex={(index: number) => {
                                this.setState(prevState => ({
                                    ...prevState,
                                    confinement: {
                                        ...confinement,
                                        primaryDiagnosisIndex: index
                                    }
                                }));
                            }} />
                    ) : null

                ) : this.state.tabSelected === 5 ? (
                    <PhysiciansSection 
                        physicians={confinement.physicians}
                        state={this.state.physicianState}
                        physiciansList={this.doctors}
                        diagnosesList={confinement.diagnoses}
                        refreshPhysiciansStateChange={() => {
                            this.setState({confinement: confinement});
                        }} />

                ) : this.state.tabSelected === 6 ? (
                    <ProceduresSection
                        procedures={confinement.procedures}
                        state={this.state.procedureState}
                        proceduresList={this.cptCodes}
                        diagnosesList={confinement.diagnoses}
                        physiciansList={this.doctors}
                        refreshProceduresStateChange={() => {
                            this.setState({confinement: confinement});
                        }} />

                ) : this.state.tabSelected === 7 ? (
                    <DischargeDetailsSection 
                        dateAdmitted={confinement.dateAdmitted}
                        coverage={confinement.coverage} 
                        ticketNo={confinement.ticketNo} 
                        paidBy={confinement.paidBy} 
                        philhealthAmount={confinement.philhealthAmount}
                        finalBill={confinement.finalBill} 
                        dischargeDate={confinement.dischargeDate}
                        onUpdateTicketStateChange={(attrName: string, value: any) => {
                            this.setState(prevState => ({
                                ...prevState,
                                confinement: {
                                    ...confinement,
                                    [attrName]: value
                                }
                            }));
                        }} />
                    
                ) : this.state.tabSelected === 8 ? (
                    <OtherDetailsSection 
                        items={confinement.items}
                        state={this.state.otherDetailState}
                        refreshItemsStateChange={() => {
                            this.setState({confinement: confinement});
                        }} />
                    
                ) : this.state.tabSelected === 9 ? (
                    <UploadedDocumentsSection 
                        files={confinement.files}
                        state={this.state.fileState}
                        refreshFilesStateChange={() => {
                            this.setState({confinement: confinement});
                        }} />
                    
                    
                    
                ) : null}
            </div>
        );
    }









    
    renderInquiryContent(inquiries: InquiryData[]): JSX.Element {
        return (
            <div className='main-content'>
                <CustomTabs 
                    tabList={this.inquiryTabList}
                    selectedTab={this.state.tabSelected}
                    setSelectedTab={(index: number) => {
                        this.setState({tabSelected: index});
                    }} />
                    
                {this.state.tabSelected === 1 && this.state.ticket ? (
                    this.state.permission.canUpdate ? (
                        <UpdateInquirySection 
                            inquiries={inquiries}
                            refreshInquiryState={() => {
                                this.setState({inquiries: inquiries});
                            }} />
                    ) : (
                        <ViewInquirySection
                            inquiries={inquiries} />
                    )
                    
                ) : this.state.tabSelected === 2 ? (
                    <CommentSection
                        ticketId={this.props.match.params['ticketId']}
                        comments={this.state.comments}
                        refreshComments={() => {
                            this.setState({comments: this.state.comments});
                        }} />
                ) : this.state.tabSelected === 3 && this.state.member ? (
                    <CheckBenefitSection
                        memberId={this.state.member.memberId} />
                ) : this.state.tabSelected === 4 ? (
                    <PartnerNetworkSection 
                        clientId={
                            this.state.member && this.state.member.client ? this.state.member.client.id : 
                            this.state.client ? this.state.client.id : ''} />
                ) : this.state.tabSelected === 6 ? (
                    <CheckUtilizationSection />
                ) : this.state.tabSelected === 7 ? (
                    <ReviewTicketsSection 
                        memberId={this.state.member ? this.state.member.memberId : undefined}
                        clientId={this.state.client ? this.state.client.corporateAccountNo: undefined} />
                ) : this.state.tabSelected === 8 ? (
                    <CheckContractSection 
                        memberId={this.state.member ? this.state.member.memberId : undefined}
                        clientId={this.state.client ? this.state.client.corporateAccountNo : undefined} />
                ) : null}
            </div>
        );
    }
}