import * as React from 'react';
import './component-style.scss';
import { Radio } from '@material-ui/core';

interface IProps {
    label?: string;
    checkedCondition: boolean;
    disabled?: boolean;
    onChange: () => void;
}

export class CustomRadio extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <div className='check-box-area'>
                <Radio
                    disabled={this.props.disabled}
                    className='check-box'
                    checked={this.props.checkedCondition}
                    onChange={(_event: React.ChangeEvent<HTMLInputElement>) => {
                        this.props.onChange();
                    }} />
                {this.props.label ? (
                    <span className='text' style={{paddingLeft: '10px'}}>{this.props.label}</span>
                ) : null}
            </div>
        );
    }
}