/**
 * app.ts
 *
 * This is the entry file for the application, only setup and boilerplate
 * code.
 */

import 'react-app-polyfill/ie11';
import 'react-app-polyfill/stable';

// Import all the third party stuff
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import FontFaceObserver from 'fontfaceobserver';
import 'sanitize.css/sanitize.css';



// Import root app
import App from 'Containers/App';

// Import Language Provider
// import MessageProvider from 'Containers/LanguageProvider';

// Load the favicon and the .htaccess file
// import '!file-loader?name=[name].[ext]!./images/favicon.ico';
// import 'file-loader?name=.htaccess!./.htaccess';

import { Components, Containers, Utils } from '@hims/core';
// import { Containers, Utils } from '@hims/core';
import { translationMessages } from 'i18n';
import createReducer from './reducers';

const { configureStore, history } = Utils;
const MessageProvider = Containers.Message.Provider;
const { ServiceWorker } = Components.Services;

// Import i18n messages

// Observe loading of Open Sans (to remove open sans, remove the <link> tag in
// the index.html file and this observer)
const openSansObserver = new FontFaceObserver('Open Sans', {});

// Create redux store with history
const initialState = {};
const store = configureStore(createReducer, initialState, history);
const MOUNT_NODE = document.getElementById('app') as HTMLElement;

const render = (messages: any, Component = App) => {
  const Document: React.ComponentType<Containers.Document.IProps<any, any>> = Containers.Document.Component;
  ReactDOM.render(
    // tslint:disable-next-line:jsx-wrap-multiline
      <Document Component={Component} store={store} componentProps={{}}>
        <MessageProvider messages={messages}>
          <Component />
        </MessageProvider>
      </Document>
    ,
    MOUNT_NODE,
  );
};

declare const module: any;

export default ()=>{
  // When Open Sans is loaded, add a font-family using Open Sans to the body
  openSansObserver.load(undefined, 60000).then(() => {
    document.body.classList.add('fontLoaded');
  });

  if (module.hot) {
    module.hot.accept(['./i18n', './Containers/App'], () => {
      ReactDOM.unmountComponentAtNode(MOUNT_NODE);
      // tslint:disable-next-line:max-line-length
      const App = require('./Containers/App').default; // https://github.com/webpack/webpack-dev-server/issues/100
      render(translationMessages, App);
    });
    // Make reducers hot reloadable, see http://mxs.is/googmo
    /* istanbul ignore next */
    module['hot'].accept('./reducers', () => {
      store.replaceReducer(createReducer(store.injectedReducers));
    });
  }
  // Chunked polyfill for browsers without Intl support
  if (!(window as any).Intl) {
    new Promise(resolve => {
      resolve(import('intl'));
    })
      .then(() =>
        Promise.all([
          import('intl/locale-data/jsonp/en.js'),
          import('intl/locale-data/jsonp/de.js'),
        ]),
      )
      .then(() => render(translationMessages))
      .catch(err => {
        throw err;
      });
  } else {
    render(translationMessages);
  }

  // Install ServiceWorker and AppCache in the end since
  // it's not most important operation and if main code fails,
  // we do not want it installed
  // if (process.env.NODE_ENV === 'production') {
  //   require('offline-plugin/runtime').install();
  // }

  // If you want your app to work offline and load faster, you can change
  // unregister() to register() below. Note this comes with some pitfalls.
  // Learn more about service workers: https://bit.ly/CRA-PWA
  ServiceWorker.register();
}
