import * as React from 'react';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { Grid, TableCell } from '@material-ui/core';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';

import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';
import { DetailedNumberField } from 'Pages/CustomerCarePage/Components/DetailedNumberField';
import { CRUDTable } from 'Pages/CustomerCarePage/UpdateTicketPage/Components/CRUDTable';
import { DatePicker } from 'Pages/CustomerCarePage/Components/DatePicker';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { DropdownChoice } from 'Pages/CustomerCarePage/Models';
import { AccommodationData } from '../UpdateTicketProps';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';

interface IProps {
    accommodations: AccommodationData[];
    state: AccommodationData;
    facilitiesList: DropdownChoice[];
    provider: DropdownChoice;
    dateAdmitted: Date;
    confinementKind: string;
    refreshAccommodationsState: () => void;
}

export class AccommodationsSection extends React.Component<IProps> {

    confinementTypes: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.confinementTypes = [
            { value: '1', label: 'Elective / Scheduled' },
            { value: '2', label: 'ER leading to confinement' }
        ];
    }

    onFormatTableHeader = (colIndex: number, value: any) => {
        return (
            colIndex === 1 || colIndex === 2 ? (
                <TableCell style={{textAlign: 'right', minWidth: '200px'}}>{value}</TableCell>
            ) : (
                <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
            )
        );
    }

    onFormatCRUDTable = (colIndex: number, value: any) => {
        const textAlign = (colIndex !== 0 ? 'right' : 'left');
        return (
            <div style={{textAlign: textAlign}}>
                {colIndex === 2 ? 
                    GlobalFunction.formatNumber(value, true) + ' PHP' : 
                    value}
            </div>
        );
    }

    getTotalCost = () => {
        let totalCost = 0;
        this.props.accommodations.map(accommodation => {
            totalCost += accommodation.costPerDay * accommodation.days;
        });

        return totalCost;
    }

    renderConfinementDetails(): JSX.Element {
        return (
            <div className='sub-section'>
                <div className='header-2'>CONFINEMENT DETAILS</div>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <DetailedDropdown 
                            placeholder='Provider'
                            value={this.props.provider}
                            choices={this.props.facilitiesList}
                            disabled={true}
                            onChange={(_value: DropdownChoice) => null}/>
                    </Grid>
                    <Grid item xs={3}>
                        <DatePicker 
                            placeholder='Date Admitted'
                            value={this.props.dateAdmitted}
                            disabled={true}
                            onChange={(_value: Date) => null}/>
                    </Grid>
                    <Grid item xs={3}>
                        <Dropdown 
                            placeholder='Kind of Confinement'
                            value={{ value: '', label: this.props.confinementKind }}
                            choices={this.confinementTypes}
                            disabled={true}
                            onChange={(_value: DropdownChoice) => null}/>
                    </Grid>
                </Grid>
            </div>
        );
    }

    renderAddRoomAccommodation(): JSX.Element {
        return (
            <div className='sub-section'>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <InputField 
                            placeholder='Room Type'
                            value={this.props.state.roomType}
                            onChange={(value: string) => {
                                this.props.state.roomType = value;
                                this.props.refreshAccommodationsState();
                            }}/>
                    </Grid>
                    <Grid item xs={3}>
                        <NumberField 
                            placeholder='No. of Days'
                            value={this.props.state.days}
                            // error={this.props.state.days < 1 || isNaN(this.props.state.days)}
                            onChange={(value: number) => {
                                this.props.state.days = value;
                                this.props.refreshAccommodationsState();
                            }}/>
                    </Grid>
                    <Grid item xs={3}>
                        <DetailedNumberField 
                            placeholder='Cost Per Day'
                            detail='PHP'
                            isFloat={true}
                            value={this.props.state.costPerDay}
                            onChange={(value: number) => {
                                this.props.state.costPerDay = value;
                                this.props.refreshAccommodationsState();
                            }}/>
                    </Grid>
                </Grid>
                <div style={{paddingTop: '5px'}} />
                {this.props.state.days > 0 ? (
                    <TextButton 
                        label='Add room' 
                        icon={faPlus}
                        onClick={() => {
                            var state = this.props.state;
                            const tempData = {
                                roomType: state.roomType,
                                days: state.days,
                                costPerDay: state.costPerDay
                            }
                            this.props.accommodations.unshift(tempData);
                            this.props.state.roomType = '';
                            this.props.state.days = 0;
                            this.props.state.costPerDay = 0;
                            this.props.refreshAccommodationsState();
                        }}/>
                ) : null}
            </div>
        );
    }

    render(): JSX.Element {

        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Add Room Accommodation</div>
                {this.renderAddRoomAccommodation()}
                <div className='sub-section' />
                {this.renderConfinementDetails()}
                <div className='sub-section' style={{overflowX: 'auto'}}>
                    <CRUDTable 
                        className='custom-table'
                        placeholder='ROOM ACCOMMODATIONS'
                        cols={['Room Type', 'No. of Days', 'Cost Per Day']} 
                        rows={this.props.accommodations}
                        onSaveEditRow={(index: number, data: any) => {
                            this.props.accommodations[index] = data;
                            this.props.refreshAccommodationsState();
                        }}
                        onDeleteRow={(index: number) => {
                            this.props.accommodations.splice(index, 1);
                            this.props.refreshAccommodationsState();
                        }}
                        onHeaderFormat={(colIndex: number, colName: string) => {
                            return this.onFormatTableHeader(colIndex, colName);
                        }} 
                        onFormat={(_rowIndex: number, colIndex: number, value: any) => {
                            return this.onFormatCRUDTable(colIndex, value);
                        }}
                        editField={(colIndex: number, currentVal: any, onChange: any) => {
                            if (colIndex === 0) {
                                return (
                                    <InputField
                                        value={currentVal}
                                        onChange={(value: string) => onChange(value)}/>
                                );
                            } else if (colIndex === 1) {
                                return (
                                    <NumberField
                                        value={currentVal}
                                        onChange={(value: number) => onChange(value < 1 ? 1 : value)}/>
                                );
                            } else {
                                return (
                                    <DetailedNumberField
                                        detail='PHP'
                                        isFloat={true}
                                        value={currentVal}
                                        onChange={(value: number) => onChange(value)}/>
                                );
                            }
                        }} />
                    <div className='sub-section'>
                        <div style={{float: 'right'}}>
                            <span className='small-text'>TOTAL AMOUNT</span>
                            <span style={{marginLeft: '1rem'}} />
                            <span className='header-2'>{GlobalFunction.formatNumber(this.getTotalCost(), true) + ' PHP'}</span>
                        </div>
                    </div>
                </div>
                <div className='section' />
            </div>
        );
    }
}