//#region Global Imports
import * as React from 'react';
import {useState} from 'react'
//import { BenefitsTable } from './BenefitsTable';
import { faSort,faSearch } from '@fortawesome/free-solid-svg-icons';
import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import { PositionProperty } from 'csstype';
//import { Components } from '@hims/core';
//#endregion Global Imports
import Link from '@material-ui/core/Link';
import { Loader } from 'Components/UI/LoadingIndicator';
import Typography from '@material-ui/core/Typography';
//import { defaultProps } from './Constants';
import { API } from '../../API';
import { makeStyles } from '@material-ui/styles'; 
import { TableCell,TableRow } from '@material-ui/core';
import { BenefitsTable2 } from './BenefitsTable2'
import { IconButton } from './IconButton';
import { InputField } from './InputField';
import {
      GridHorizontalDivider,
  } from './GridHorizontalDivider';
  import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    TextField,
} from '@material-ui/core';
interface IProps {
    contractID:string;
}
const filtersForApi = {
    where: {}
  }
let currentSortAttr = "";
const tablestyle= makeStyles(()=>({
    benefitTable:{	
        boxShadow: '0px 3px 6px #00000029',
},
searchField: {
    backgroundColor: 'white',
    marginLeft:'4%',
    width:'92%',
},
searchInputAdornment:{
    color: '#00000061',
      marginRight: '8px',
},
}));

export const ExclusionsSection: React.FC<IProps> = (): JSX.Element | null => {
	//const { contractID } = props;
    const [exclusionList,setExclusionList] = useState<any[]>([]);
    const [contractID,setContractID]=useState('');
    const [renderTable,setRenderTable] = useState(true)
    const [filters, setFilters] = useState<any[]>([]);
    const [ exclusionTable, setExclusionTable ] = useState<any>();
    const [ tabList, setTablist] = useState<any>();
    const [ loading,setLoading] = useState(false);
    //const [ totalCount, setTotalCount ] = useState(0);
    const [typingTimeout, setTypingTimeout] = useState<any>(0);
    const [selectedTab, setSelectedTab] = useState('BASE');
    const exclusionstyle= tablestyle();
    const [largeSearch , setLargeSearch] = useState('');
    let columns: any = [	{ name: 'parent_name', title: 'Type' }, 
							{ name: 'name', title: 'Item' }
							
                        ];
    const getFilteredTickets = (columns: any) => {
        let filteredTickets = exclusionList;
        if(largeSearch.trim()!=='')
        {
            console.log('largeSearch',largeSearch,filteredTickets)
            filteredTickets = filteredTickets.filter((col) =>{
                return col['name'].toString().toLowerCase().includes(largeSearch.toLocaleLowerCase().trim()) || 
                col['parent_name'].toString().toLowerCase().includes(largeSearch.toLocaleLowerCase().trim())
            })
        }
        columns.map(column => {
          filteredTickets = filteredTickets
            .filter(ticket =>
                filters[column.name] ? (
                    filters[column.name] === '' ||
                (ticket[column.name] &&
                  ticket[column.name].toString().toLowerCase().includes(filters[column.name].toLowerCase())
                )
              ) : true
            )
         });
         filteredTickets = filteredTickets.filter((col) =>{
             return col.plan_type === selectedTab
         })

        return filteredTickets;
      }

    const renderExclusionTable = () => {
        /*return (<BenefitsTable 
            rows={exclusionList}
            viewOnly={true} 
            disableSelect  
            columns={columns} 
            formattedColumns={formattedColumns} />)*/
        return(
            <BenefitsTable2
                className='ticket-list-table'
                canOverflow={true}
                rows={getFilteredTickets(columns)}
                stickyHeader={true}
                renderHeader={() => renderCustomTableHeader(columns, columns)}
                renderRow={(row: any, index: number) => renderCustomTableRow(row, index, columns)}
                rowsPerPageOptions={[5, 10, 15]}
            />
        )
    }
    const handleFiltersOnChange = () => {
        if (typingTimeout) {
          clearTimeout(typingTimeout);
        }
    
        setTypingTimeout (setTimeout(function () {
                handleLoadsFromAPI();
              }, 2000)
        );
      }
    const renderTabs = (tabs:string[]) =>{
        console.log('rendertabs',tabs)
        const tabelem:any = tabs.map((col)=>{
            console.log('item',col)
            return (<Typography style={{display:'inline-block',marginRight:'10px'}}>
                    <Link onClick={()=>{setSelectedTab(col);handleLoadsFromAPI();}}><span>{col}</span></Link>
                </Typography>)
        })
        console.log('tabelem',tabelem)
        return tabelem;
    }

    if(renderTable){
        setExclusionTable(renderExclusionTable());
        setRenderTable(false);
      }

    const sortProposals = (attrName: string) => { 
        let mask = "";
        if(attrName == 'account'){
          mask = "client_registered_name";
        }else if(attrName == 'effectivity_date'){
          mask = "custom_metadata.coverage_start_date";
        } else if(attrName == 'version'){
          mask = "custom_metadata.version";
        }else if(attrName == 'date_created'){
          mask = "custom_metadata.date_created";
        }else if(attrName == 'date_modified'){
          mask = "custom_metadata.date_updated";
        }
  
        filtersForApi['order'] = {};
        if(currentSortAttr == mask){
          filtersForApi['order']=  `${mask} desc`
        }else{
          filtersForApi['order']=  `${mask} asc`
        }
        handleLoadsFromAPI();
        currentSortAttr = mask;
        
      }
    const renderCustomTableHeader = (columns: any, fixedColumns: any) => {
        let dockedColumnsCounter = 0;
            return (
              <>
              {columns.map((column, index) => {
                const matchedColumn = fixedColumns.filter(col => col === column.name)[0];
                let nextColumnIsFixed = true;
                if (matchedColumn && columns[index + 1]) {
                  nextColumnIsFixed = fixedColumns.filter(col => col ===  columns[index + 1].name)[0] ? true : false;
                }
                const style = {
                  left: matchedColumn ? dockedColumnsCounter * 125 : 0,
                  zIndex: matchedColumn ? 125 : 0,
                  width:column.name==='version'? '100px':'150px',
                  paddingRight : '0px',
                  paddingLeft : '10px',
                  borderRight: !nextColumnIsFixed ? '#9EA3AC 2px solid' : undefined
                };
                if (matchedColumn) {
                  dockedColumnsCounter++;
                }
                return (
                  <TableCell style={style}>
                    <span className='header-2'>{column.title}</span>
                    <div style={{float: 'right'}}>
                        <IconButton
                            icon={faSort}
                            onClick={() => {
                                sortProposals(column.name);
                                setRenderTable(true);
                                //setLoading(false);
                            }} />
                    </div>
                    <InputField
                        value={filters[column.name]}
                        onChange={(value: string) => {
                            filters[column.name] = value;
                            setFilters(filters);
                            handleFiltersOnChange();
                            //setRenderTable(true);
                            // this.setState(prevState => ({...prevState, [attrName]: value}));
                        }} />
                  </TableCell>
                );
              })}
              </>
            );
    }

    const handleLoadsFromAPI = () =>{
        setLoading(true);
        API.get('benefit-plans/tree/'+contractID,
        (response)=>{
            console.log('benefits',response);
            console.log('exclusions',response.filter((element)=>{return element.tree_path[3]==='EX'}))
            const parent = response.filter((element)=>{
                                return element.level===5 
                                && element.tree_path[3]==='EX' })
            console.log('parents',parent)
            const tempexclusion:any[] = []
            parent.map((col)=>{
                col.children.forEach((item)=>{
                    const sel = response.filter((element)=>{return element._id===item})
                    tempexclusion.push({
                        name: sel[0].name,
                        parent_name: col.name,
                        code: sel[0].code,
                        parent_code: col.code,
                        plan_type: col.tree_path[2]
                    })
                })
            })
            const tabs = response.filter((element)=>{
                return element.level===3})
            const tabarray:string[] = []
            tabs.map((col)=>{
                tabarray.push(col.code)
            })
            console.log('tabs',tabarray)
            setTablist(renderTabs(tabarray));
            setExclusionList(tempexclusion);
            //setExclusionTable(renderExclusionTable());
            
            console.log('exclusions',tempexclusion);
        },
        (error)=>{
            console.log(error)
        });
    }
    
    React.useEffect(() => { 
        //setSelectedRows(sgncntsrows);
        
        const tempClientData = localStorage.getItem('clientData')
        //console.log('tempClientData',tempClientData)
        if(typeof tempClientData === 'string')
        {
            console.log('check',JSON.parse(tempClientData));
            const tempClientDataarray = JSON.parse(tempClientData)
            if(tempClientDataarray.latest_contract)
            {
                setContractID(tempClientDataarray.latest_contract._id)
            }
        }
        
    }, []);

  React.useEffect(() => { 
      handleLoadsFromAPI();
  }, [contractID]);

    React.useEffect(() => { 
      setLoading(false);
        setExclusionTable(renderExclusionTable());
    }, [exclusionList]);
	//console.log(rows);

	
    const rowcustomstyle = {
        left: 0,
        position: 'relative' as PositionProperty,
        fontSize:'12px',
        //minWidth: '125px',
        //maxWidth: '125px',
        minWidth: '10px',
        maxWidth: '300px',
        paddingRight : '0px',
        paddingLeft : '10px',
        };
	//let formattedColumns = {};

	/*if (!viewOnly) {
		columns.push({	
			name: 'included', 
			title: 'Include item?'
		});

		formattedColumns = {
			exclusions_checkbox: ['included'],
		};
    }*/
    const renderCustomTableRow = (row: any, index: number, columns: any) => {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
          <TableRow
            style={{backgroundColor: backgroundColor}}
            >
            {columns.map((column) => {
              return (
                
                <TableCell style={rowcustomstyle} onClick={() => {
                  console.log('href');
                }}>
                  {row[column.name]}
                </TableCell>
                
              );
            })}
          </TableRow>
        );
    }
    
	return (
		<React.Fragment>
      { loading ?
          (<Loader/>) : null
        }
            <div className='sub-section'>
                <Grid item sm={12}>
                    <Grid container spacing={2} style={{ marginTop: '27.5px' }}>
                    <Grid item sm={12} 
                        style={{marginBottom:'10px',
                        fontSize:'14px',
                        marginTop:'14px',
                        marginLeft:'2%',
                        fontWeight:'bold'}}>
                        Check Exclusions
                    </Grid>
                    <Grid item sm={12} 
                        >
                        <div style={{borderBottom:'1px solid #C3C3C3'
                        ,marginLeft:'4%',width:'92%'}}>
                            {tabList}
                        </div>
                        
                    </Grid>
                    </Grid>
                </Grid>
                <GridHorizontalDivider height="30px" />
                <Grid item sm={12}>
                <TextField
                    id="history_search"
                    placeholder="Search a benefit or benefit category"
                    className={exclusionstyle.searchField}
                    margin="dense"
                    variant="outlined"
                    fullWidth
                    value={largeSearch}
                    onChange={(event:any)=>{
                        setLargeSearch(event.target.value.trim())
                        handleFiltersOnChange();}}
                    inputProps={{ 'aria-label': 'bare' }}
                    InputProps={{
                    startAdornment: (
                        <FontAwesomeIcon
                        icon={faSearch}
                        className={exclusionstyle.searchInputAdornment}
                        />
                    ),
                    }}
                />
                </Grid>

                <Grid item sm={12}>
                    <div style={{marginLeft:'4%',width:'92%'}}>
                        {exclusionTable}
                    </div>
                    
                </Grid>
            </div>
			
		</React.Fragment>
	);
};

//ExclusionsSection.defaultProps = defaultProps;