import * as React from 'react';
import './component-style.scss';

interface IProps {
    value: number;
    onChange: (value: number) => void;

    placeholder?: string;
    error?: boolean;
    isFloat?: boolean;
    disableDelimiter?: boolean;
}

interface IStates {
    value: string;
}

export class NumberField extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            value: this.props.value.toString()
        }
    }

    componentWillReceiveProps(props: IProps) {
        let targetNewValue = props.value ? (
            !this.props.disableDelimiter ? 
                props.value.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,') : 
                props.value.toString()) : '';

        if (targetNewValue.charAt(targetNewValue.length - 4) === '.') {
            targetNewValue = props.value.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
        }

        if (targetNewValue !== this.state.value) {
            this.setState({value: 
                this.props.isFloat && this.state.value.charAt(this.state.value.length - 1) === '.' ?
                    `${targetNewValue}.` :
                    targetNewValue});
        }
    }

    render(): JSX.Element {
        const props = this.props;
        const dynamicStyle = {
            borderColor: (props.error ? '#E53935' : undefined)
        };

        return (
            <div className='input-field-container'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : null}
                <input
                    className='text-field'
                    style={dynamicStyle}
                    value={this.state.value} 
                    onFocus={() => {
                        if (this.props.value == 0) {
                            this.setState({value: ''});
                        }
                    }}
                    onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                        let value = event.target.value;
                        let numberValue = parseFloat(value.replace(/,|-/g, ''));
                        if (!isNaN(numberValue)) {
                            numberValue = numberValue < 1000000000 ? numberValue : this.props.value;
                        }
                        let stringValue = !isNaN(numberValue) ? (
                            this.props.isFloat && value.charAt(value.length - 1) === '.' ?
                                `${numberValue}.` :
                                numberValue.toString()
                        ) : '';
                        const newStringValue = stringValue.replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
                        this.setState({value: this.props.disableDelimiter ? numberValue.toString() : newStringValue}, () => {
                            if (this.props.isFloat) {
                                this.props.onChange(!isNaN(numberValue) ? numberValue : 0);
                            } else {
                                this.props.onChange(!isNaN(numberValue) ? numberValue : 0);
                            }
                        });
                    }} />
            </div>
        );
    }
}