import * as React from 'react';
import { Grid, TableCell, TableRow } from '@material-ui/core';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { API } from 'Pages/CustomerCarePage/API';
import { PartialBenefitData } from 'Pages/CustomerCarePage/Models';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { faSort } from '@fortawesome/free-solid-svg-icons';
import { LabelDisplayedRowsArgs } from '@material-ui/core/TablePagination';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';

interface IProps {
    memberId: string;
}

interface IStates {
    benefitCategory: string;
    benefitName: string;
    limitType: string;
    limit: string;

    benefitListUpdated: boolean;
}


export class CheckBenefitSection extends React.Component<IProps, IStates> {

    benefits: PartialBenefitData[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            benefitCategory: '',
            benefitName: '',
            limitType: '',
            limit: '',
            benefitListUpdated: false
        }

        this.benefits = [];
    }

    componentDidMount() {
        this.loadBenefitsData();
    }

    loadBenefitsData(): void {
        API.get(`customer-care/members/${this.props.memberId}/benefits`, 
            (response) => {
                console.log(response);
                this.benefits = this.combineNodes(
                        response.filter(benefit => benefit.level == 6),
                        response.filter(benefit => benefit.level == 7)
                    ).map(benefit => {
                        return {
                            category: benefit.parent_name,
                            name: benefit.name,
                            limitType: (benefit.limits.length > 0 ? benefit.limits[0].type : 'None'),
                            limit: (benefit.limits.length > 0 ? (
                                benefit.limits[0].limit + ' ' + benefit.limits[0].limit_unit
                            ) : '---')
                        }
                    });
                this.setState({benefitListUpdated: true});

                /* Testing only */
                console.log(
                    this.combineNodes(
                        response.filter(benefit => benefit.level == 6),
                        response.filter(benefit => benefit.level == 7)
                    )
                );
            },
            (error) => {
                console.log(error);
            });
    }

    combineNodes = (parentNodes, childNodes) => {
        const nodes = parentNodes.reduce ((result, parent) => {
            parent.children.map ((child) => {
            let foundChild = childNodes.find ((childNode) => child === childNode._id);
        
            if (foundChild) {
                let child = Object.assign({}, foundChild);
        
                child['parent_name'] = parent.name;
                child['parent_code'] = parent.code;
                child['disabled'] = false;
                result.push(child);
            }
            });
        
            return result;
        }, new Array());
        
        return nodes;
    };

    getFilteredBenefitsData = () => {
        return (
            this.benefits
                .filter(benefit => 
                    this.state.benefitCategory === '' || 
                    benefit.category.toLowerCase().includes(this.state.benefitCategory.toLowerCase()))
                .filter(benefit => 
                    this.state.benefitName === '' || 
                    benefit.name.toLowerCase().includes(this.state.benefitName.toLowerCase()))
                .filter(benefit => 
                    this.state.limitType === '' || 
                    benefit.limitType.toLowerCase().includes(this.state.limitType.toLowerCase()))
                .filter(benefit => 
                    this.state.limit === '' || 
                    benefit.limit.toLowerCase().includes(this.state.limit.toLowerCase()))
        );
    }
    
    sortBenefits(attrName: string, isAsc: boolean): void {
        this.benefits.sort((a: PartialBenefitData, b: PartialBenefitData) => 
            (a[attrName] && b[attrName] && a[attrName].toString().toLowerCase() > b[attrName].toString().toLowerCase()) ? 
                (isAsc ? 1 : -1) : (isAsc ? -1 : 1)
        );
    }

    render(): JSX.Element {
        return (
            <div className='section'>

                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Check Benefits</div>
                <div className='sub-section' />
                <Grid container className='sub-section' spacing={5}>
                    {this.renderSideBar()}

                    {this.state.benefitListUpdated ? (
                        this.renderBenefitsList()
                    ) : (
                        <Grid item xs={10}>
                            <Loading height='10rem'/>
                        </Grid>
                    )}
                    
                </Grid>
                <div className='section' />
            </div>
        );
    }

    renderSideBar(): JSX.Element {
        const rightBorderStyle = {
            borderRight: '#3C394A22 solid 1px'
        }
        return (
            <Grid item xs={2} style={rightBorderStyle}>
                    <div className='small-text'>Contract ID</div>
                    <div className='header-1'>{'2019-0928*'}</div>
                <div className='sub-section'>
                    <div className='small-text'>Room and Board</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>COVERAGE</div>
                    <div className='sub-section'>
                        <TextButton 
                            label='APE' 
                            onClick={() => {
                                this.setState({benefitListUpdated: false});
                                this.loadBenefitsData();
                            }} />
                    </div>
                    <div className='sub-section'>
                        <TextButton 
                            label='PPE' 
                            onClick={() => {
                                this.setState({benefitListUpdated: false});
                                this.loadBenefitsData();
                            }} />
                    </div>
                    <div className='sub-section'>
                        <TextButton 
                            label='Inpatient' 
                            onClick={() => {
                                this.setState({benefitListUpdated: false});
                                this.loadBenefitsData();
                            }} />
                    </div>
                    <div className='sub-section'>
                        <TextButton 
                            label='Outpatient' 
                            onClick={() => {
                                this.setState({benefitListUpdated: false});
                                this.loadBenefitsData();
                            }} />
                    </div>
                    <div className='sub-section'>
                        <TextButton 
                            label='Emergency' 
                            onClick={() => {
                                this.setState({benefitListUpdated: false});
                                this.loadBenefitsData();
                            }} />
                    </div>
                </div>

                <div className='sub-section'>
                    <div className='header-1 light-text'>RIDERS</div>
                </div>

            </Grid>
        );
    }

    renderBenefitsList(): JSX.Element {

        return (
            <Grid item xs={10}>
                <div className='sub-section'>
                    <CustomTable
                        rows={this.getFilteredBenefitsData()}
                        renderHeader={() => this.renderTableHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderTableRow(rowData, index)}
                        onChangeInfo={(info: LabelDisplayedRowsArgs) => 
                            `${info.from}-${info.to} out of ${info.count} benefits`}
                        labelRowsPerPage='Show'
                        rowsPerPageOptions={[10, 25, 50]} />
                </div>

            </Grid>
        );
    }

    renderTableHeader(): JSX.Element {

        return (
            <>
            {this.renderHeaderColumn('benefitCategory', 'Benefit Category', '150px')}
            {this.renderHeaderColumn('benefitName', 'Benefit', '250px')}
            {this.renderHeaderColumn('limitType', 'Limit Type', '100px')}
            {this.renderHeaderColumn('limit', 'Limit', '100px')}
            </>
        );
    }

    renderTableRow(benefit: PartialBenefitData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{benefit.category}</TableCell>
                <TableCell>{benefit.name}</TableCell>
                <TableCell>{benefit.limitType}</TableCell>
                <TableCell>{benefit.limit}</TableCell>
            </TableRow>
        );
    }

    renderHeaderColumn(attrName: string, headerText: string, width: string): JSX.Element {
        return (
            <TableCell style={{width: width}}>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            this.sortBenefits(attrName, true);
                            this.setState({benefitListUpdated: true});
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        this.setState(prevState => ({...prevState, [attrName]: value}));
                    }} />
            </TableCell>
        );
    }
}