import * as React from 'react';
import { ClientData } from 'Pages/CustomerCarePage/Models';
import { Grid } from '@material-ui/core';
import { CustomCheckbox } from 'Pages/CustomerCarePage/Components/CustomCheckbox';
import { CustomRadio } from 'Pages/CustomerCarePage/Components/CustomRadio';

interface IProps {
    client: ClientData;
}

export class CardSortingSection extends React.Component<IProps> {

    render() : JSX.Element {
        const sortingOfCards = this.props.client.servicingInstructions ? 
                               this.props.client.servicingInstructions.sortingOfCards : 
                               undefined;

        return (
            <div className='sub-section'>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <div className='text'>Card Sorting</div>
                        {sortingOfCards && sortingOfCards.cardSorting ? (
                            <div style={{boxShadow: '0px 3px 6px #00000029'}}>
                                {sortingOfCards.cardSorting.map((i, index) => {
                                    const backgroundColor = (index % 2 === 1 ? '#FFFFFF' : '#F6F7FA');
                                    return <div style={{backgroundColor: backgroundColor, padding: '8px'}}>{i}</div>
                                })}
                            </div>
                        ) : null}
                    </Grid>
                    <Grid item xs={6}>
                        <div className='sub-section'>
                            <div className='text'>Card Packing</div>
                            <CustomRadio 
                                label='Envelope'
                                disabled
                                checkedCondition={
                                    sortingOfCards && 
                                    sortingOfCards.cardPacking &&
                                    sortingOfCards.cardPacking === 'envelope' ? true : false}
                                onChange={() => {}} />
                            <CustomRadio 
                                label='Plastic'
                                disabled
                                checkedCondition={
                                    sortingOfCards && 
                                    sortingOfCards.cardPacking &&
                                    sortingOfCards.cardPacking === 'plastic' ? true : false}
                                onChange={() => {}} />
                            <CustomRadio 
                                label='With Flyer'
                                disabled
                                checkedCondition={
                                    sortingOfCards && 
                                    sortingOfCards.cardPacking &&
                                    sortingOfCards.cardPacking === 'with flyer' ? true : false}
                                onChange={() => {}} />
                        </div>
                        <div className='sub-section'>
                            <div className='text'>Pack according to</div>
                            <CustomCheckbox
                                label='Team Leader'
                                disabled
                                value={
                                    sortingOfCards && 
                                    sortingOfCards.packAccordingTo &&
                                    sortingOfCards.packAccordingTo.includes('team_leader') ? true : false}
                                onChange={(_value: boolean) => {}} />
                             <CustomCheckbox
                                label='Family'
                                disabled
                                value={
                                    sortingOfCards && 
                                    sortingOfCards.packAccordingTo &&
                                    sortingOfCards.packAccordingTo.includes('family') ? true : false}
                                onChange={(_value: boolean) => {}} />
                        </div>
                    </Grid>
                </Grid>
            </div>
        );
    }
}